const generateEvents = ({
  C,
  eventGeneratorFunctions,
  getRandomValue
}) => ({
  assets,
  clubDetails,
  staff,
  startIndex
}) => {
  const randomEvents = C.events.filter(e => e.when === 'random')
  const eventsByRate = {
    common: randomEvents.filter(e => e.rate === 'common'),
    frequent: randomEvents.filter(e => e.rate === 'frequent'),
    infrequent: randomEvents.filter(e => e.rate === 'infrequent'),
    rare: randomEvents.filter(e => e.rate === 'rare')
  }
  const numberOfEvents = getRandomValue(0, 3)
  if (numberOfEvents === 0) return []
  const eventDice = getRandomValue(0, 100)
  const events = Array(numberOfEvents)
    .fill(null)
    .map((_, i) => {
      let newEvent

      // determine which rarity level of event to generate
      if (eventDice % 10 === 0) newEvent = eventsByRate.rare[getRandomValue(0, eventsByRate.rare.length - 1)]
      else if (eventDice % 8 === 0) newEvent = eventsByRate.infrequent[getRandomValue(0, eventsByRate.infrequent.length - 1)]
      else if (eventDice % 5 === 0) newEvent = eventsByRate.common[getRandomValue(0, eventsByRate.common.length - 1)]
      else newEvent = eventsByRate.frequent[getRandomValue(0, eventsByRate.frequent.length - 1)]
      
      let eventDetail = {}
      // generate the event detail by calling its generator function
      switch (newEvent.key) {
        case C.eventTypes.sponsorshipOffer:
          eventDetail = eventGeneratorFunctions[newEvent.key]({
            clubDetails
          })
          break;
        case C.eventTypes.stadiumDamaged:
          eventDetail = eventGeneratorFunctions[newEvent.key]({
            asset: assets.find(a => a.assetKey === 'stadium')
          })
          break;
        case C.eventTypes.stadiumDegraded:
          eventDetail = {
            assetCondition: assets.find(a => a.assetKey === 'stadium').assetCondition,
            assetType: 'Stadium'
          }
          break;
        case C.eventTypes.staffQuit:
          eventDetail = eventGeneratorFunctions[newEvent.key]({
            clubReputation: clubDetails.reputation,
            staffList: staff
          })
          break;
        case C.eventTypes.trainingGroundDamaged:
          eventDetail = eventGeneratorFunctions[newEvent.key]({
            asset: assets.find(a => a.assetKey === 'trainingGround')
          })
          break;
        case C.eventTypes.trainingGroundDegraded:
          eventDetail = {
            assetCondition: assets.find(a => a.assetKey === 'trainingGround').assetCondition,
            assetType: 'Training Ground'
          }
          break;
        default:
          break;
      }
      // if degraded just determine which asset and remove 1 point from it
      // i.e. no generatorFunction
      return Object.assign(
        {},
        newEvent,
        { eventDetail: eventDetail },
        { id: startIndex + i }
      )
    })

  return events
}

export default generateEvents