import { createSlice } from '@reduxjs/toolkit'
import C from './constants'
const sliceName = C.sliceName

const initialState = {
  background: C.backgrounds[0].value,
  name: '',
  wealth: C.backgrounds[0].wealth[0]
}

export const playerDetailsSlice = createSlice({
  name: sliceName,
  initialState,
  reducers: {
    resetPlayerDetailsState: state => {
      state = initialState
    },
    setBackground: (state, action) => {
      state.background = action.payload
    },
    setName: (state, action) => {
      state.name = action.payload
    },
    setWealth: (state, action) => {
      state.wealth = action.payload
    }
  }
})

export const {
  resetPlayerDetailsState,
  setBackground,
  setName,
  setWealth
} = playerDetailsSlice.actions

// selectors
export const selectBackground = state => state[sliceName].background
export const selectName = state => state[sliceName].name
export const selectWealth = state => state[sliceName].wealth

export default playerDetailsSlice.reducer
