const generateFinances = ({
  addExpenditure,
  buildSquad,
  C,
  dispatch,
  getPerson,
  getRandomValue,
  setStaffMember,
  setSquadList
}) => ({
  clubDetails
}) => {
  // START STAFF
  const staff = {}
  let staffWages = []
  C.roles.roles.staff.forEach(role => {
    if (role.required) {
      const staffMember = getPerson({
        role: role.title,
        clubDetails
      })
      staff[role.key] = staffMember
      staffWages.push(staffMember.wage)
    }
  })
  C.roles.roles.staff.forEach(role => {
    if (!role.required) {
      const chanceOfStaff = getRandomValue(0, clubDetails.reputation)
      if (chanceOfStaff > 20) { // because why not
        const staffMember = getPerson({
          role: role.title,
          clubDetails
        })
        staff[role.key] = staffMember
        staffWages.push(staffMember.wage)
      }
    }
  })
  Object.keys(staff).forEach(staffMember => {
    if (staffMember) {
      dispatch(setStaffMember({
        role: staffMember,
        person: staff[staffMember]
      }))
    }
  })
  const staffWeeklyCost = staffWages
    .reduce((total, wage) => total + wage, 0)

  dispatch(addExpenditure({
    expenditureType: C.expenditures.wages.key,
    expenditureKey: C.expenditures.wages.types.find(t => t.key === 'staff').key,
    expenditureName: C.expenditures.wages.types.find(t => t.key === 'staff').text,
    expenditureValue: staffWeeklyCost,
    expenditureWeeksRemaining: -1
  }))
  // END STAFF

  // START SQUAD
  const squad = buildSquad({ clubDetails })
  dispatch(setSquadList({ squad }))
  const squadWeeklyCost = squad
    .reduce((wageBill, player) => wageBill + player.wage, 0)
  dispatch(addExpenditure({
    expenditureType: C.expenditures.wages.key,
    expenditureKey: C.expenditures.wages.types.find(t => t.key === 'player').key,
    expenditureName: C.expenditures.wages.types.find(t => t.key === 'player').text,
    expenditureValue: squadWeeklyCost,
    expenditureWeeksRemaining: -1
  }))
  // END SQUAD
}

export default generateFinances