import { useSelector } from 'react-redux'
import { selectStaff } from './staffSlice'
import { Table } from 'react-bootstrap'
import ErrorBoundary from '../../commonComponents/ErrorBoundary'

const Staff = () => {
  const staffList = useSelector(selectStaff)
    .filter(staffMember => staffMember.role)
  const salaryTotal = staffList
    .reduce((wageBill, staffMember) => wageBill + staffMember.wage, 0)

  const renderSackButton = () => 'Sack!'

  const renderSalaryRow = () => (
    <tr>
      <td />
      <td />
      <td />
      <td />
      <td />
      <td />
      <td />
      <td>Total: </td>
      <td className='list-item-primary'>
        {`£${salaryTotal.toLocaleString()}`}
      </td>
      <td />
    </tr>
  )

  const renderStaffHeader = () => (
    <h3 className='tourney'>Staff Details</h3>
  )

  const renderStaffRows = () => staffList.map((staff, i) => (
    <tr className={(i % 2 === 0) ? 'squat-row list-item-primary' : 'squat-row list-item-secondary'} key={`squad-member-${i}`}>
      <td>{staff.name}</td>
      <td>{staff.role}</td>
      <td>{staff.rating}</td>
      <td>{staff.age}</td>
      <td>{staff.popularity}</td>
      <td>{staff.reputation}</td>
      <td>{staff.relationship}</td>
      <td>{staff.contract}</td>
      <td>{`£${staff.wage.toLocaleString()}`}</td>
      <td>
        {renderSackButton()}
      </td>
    </tr>
  ))

  const renderStaffTable = () => (
    <Table className='squad-list-table'>
      <thead className='squad-list'>
        <tr>
          <th>Name</th>
          <th>Job</th>
          <th>Rating</th>
          <th>Age</th>
          <th>Popularity</th>
          <th>Reputation</th>
          <th>Relationship</th>
          <th>Contract Length</th>
          <th>Weekly Wage</th>
          <th>Sack</th>
        </tr>
      </thead>
      <tbody>
        {renderStaffRows()}
        {renderSalaryRow()}
      </tbody>
    </Table>
  )

  return (
    <>
      <ErrorBoundary>{renderStaffHeader()}</ErrorBoundary>
      <ErrorBoundary>{renderStaffTable()}</ErrorBoundary>
    </>
  )
}

export default Staff
