import { useSelector } from 'react-redux'
import Table from 'react-bootstrap/Table'
import ErrorBoundary from '../../../commonComponents/ErrorBoundary'
import C from '../../../constants'
import { selectAssets } from '../financesSlice'

const AssetsTable = () => {
  const assets = useSelector(selectAssets)
  const assetTotal = assets.reduce((assetTotal, asset) => assetTotal + asset.assetValue, 0)
  
  const renderTotalAssets = () => (
    <tr>
      <td />
      <td />
      <td>Total Assets:</td>
      <td>{`£${assetTotal.toLocaleString()}`}</td>
    </tr>
  )
  
  const renderAssetsList = () => assets.map((asset, i) => (
    <tr className={
      (i % 2 === 0)
        ? 'squat-row list-item-primary'
        : 'squat-row list-item-secondary'
      }
      key={`asset-row-${i}`}
    >
      <td>{C.assets[asset.assetKey].text}</td>
      <td>{asset.assetName ? asset.assetName : 'None'}</td>
      <td>{asset.assetCondition}</td>
      <td>{`£${asset.assetValue.toLocaleString()}`}</td>
    </tr>
  ))

  const renderNoAssets = () => <p>The club currently owns no fixed assets.</p>

  const renderAssetsTable = () => (
    <Table>
      <thead>
        <tr>
          <td>Asset Type</td>
          <td>Asset Name</td>
          <td>Asset Condition</td>
          <td>Estimated Asset Value</td>
        </tr>
      </thead>
      <tbody>
        {renderAssetsList()}
        {renderTotalAssets()}
      </tbody>
    </Table>
  )

  return (
    <ErrorBoundary>
      {assets.length === 0 && renderNoAssets()}
      {assets.length > 0 && renderAssetsTable()}
    </ErrorBoundary>
  )
}

export default AssetsTable