const playerTransferOffer = ({
  C,
  getRandomValue
}) => ({
  id,
  nonPlayerClubs,
  squad
}) => {
  const buyingClub = nonPlayerClubs[getRandomValue(0, nonPlayerClubs.length)].name
  const player = squad[getRandomValue(0, squad.length)]
  
  return {
    buyingClub,
    id,
    player,
    type: C.events.blocking.types.find(t => t.key === 'transferOfferReceived'),
    value: 10000000 // TODO: workout
  }
}

export default playerTransferOffer