import { createSlice } from '@reduxjs/toolkit'
import C from '../../constants'
const sliceName = C.sliceNames.squad

const initialState = {
  squadList: []
}

export const squadSlice = createSlice({
  name: sliceName,
  initialState,
  reducers: {
    resetSquadState: () => {
      return initialState
    },
    setSquadList: (state, action) => {
      state.squadList = action.payload.squad
    }
  }
})

export const {
  resetSquadState,
  setSquadList
} = squadSlice.actions

// selectors
export const selectSquadList = state => state[sliceName].squadList

export default squadSlice.reducer
