/**
 * Sort by pts, then gd, then team name
 */
const sortLeague = (a, b) => {
  if (a.pts > b.pts) return -1
  if (a.pts < b.pts) return 1
  if (a.pts === b.pts) {
    if (a.gd > b.gd) return -1
    if (a.gd < b.gd) return 1
    if (a.gd === b.gd) {
      if (a.team > b.team) return 1
      if (a.team < b.team) return -1
    }
  }
  return 0 // fallback
}

const updateLeagueTable = (standings, results) => {
  let updatedTeams = []
  const update = standings.reduce((table, team, i) => {
    if (updatedTeams.find(t => t === team.team)) return table
    const result = results.find(r => r.home.team === team.team || r.away.team === team.team)
    const gd = result.home.goals - result.away.goals
    let pts = [1, 1]
    if (gd > 0) pts = [3, 0]
    if (gd < 0) pts = [0, 3]
    const homeTeam = standings.find(standing => standing.team === result.home.team)
    const awayTeam = standings.find(standing => standing.team === result.away.team)
    const homeUpdate = {
      team: homeTeam.team,
      p: homeTeam.p + 1,
      w: gd > 0 ? homeTeam.w + 1 : homeTeam.w,
      d: gd === 0 ? homeTeam.d + 1 : homeTeam.d,
      l: gd < 0 ? homeTeam.l + 1 : homeTeam.l,
      gd: homeTeam.gd + gd,
      pts: homeTeam.pts + pts[0]
    }
    const awayUpdate = {
      team: awayTeam.team,
      p: awayTeam.p + 1,
      w: gd < 0 ? awayTeam.w + 1 : awayTeam.w,
      d: gd === 0 ? awayTeam.d + 1 : awayTeam.d,
      l: gd > 0 ? awayTeam.l + 1 : awayTeam.l,
      gd: awayTeam.gd - gd,
      pts: awayTeam.pts + pts[1]
    }
    updatedTeams.push(homeTeam.team, awayTeam.team)
    const updatedTable = table.concat(homeUpdate).concat(awayUpdate)
    return updatedTable
  }, [])
  const sortedTable = update.sort(sortLeague)
  return sortedTable
}

export default updateLeagueTable
