import { useSelector } from 'react-redux'
import Table from 'react-bootstrap/Table'
import ErrorBoundary from '../../../commonComponents/ErrorBoundary'
import C from '../../../constants'
import { selectExpenditures } from '../financesSlice'

const ExpendituresTable = () => {
  const expenditures = useSelector(selectExpenditures)
  const expenditureTotal = Object.keys(expenditures)
    .reduce((exps, exp) => exps.concat(expenditures[exp]), [])
    .flat()
    .reduce((expenditureTotal, expenditure) => expenditureTotal + expenditure.expenditureValue, 0)
  
  // TODO: tidy up this godawful code
  const expendituresList = Object.keys(expenditures)
    .map(exp => ({
      type: C.expenditures[exp].text,
      expenditures: expenditures[exp]
    }))
    .filter(exp => exp.expenditures.length > 0)
  
  const renderTotalExpenditures = () => (
    <tr>
      <td />
      <td>Total Expenditures:</td>
      <td>{`£${expenditureTotal.toLocaleString()}`}</td>
    </tr>
  )
  
  // TODO: tidy up this godawful code (part deux)
  const renderExpendituresList = () => expendituresList
    .map((exp) => exp.expenditures.map((expenditure, i) => (
      <tr className={
        (i % 2 === 0)
          ? 'squat-row list-item-primary'
          : 'squat-row list-item-secondary'
        }
        key={`expenditure-row-${i}`}
      >
        <td>{expenditure.expenditureName ? expenditure.expenditureName : 'None'}</td>
        <td>{expenditure.type}</td>
        <td>{`£${expenditure.expenditureValue.toLocaleString()}`}</td>
      </tr>
    ))
  )

  const renderNoExpenditures = () => <p>The club currently has no weekly expenditures.</p>

  const renderExpendituresTable = () => (
    <Table>
      <thead>
        <tr>
          <td>Expenditure Type</td>
          <td>Expenditure Name</td>
          <td>Estimated Expenditure Value</td>
        </tr>
      </thead>
      <tbody>
        {renderExpendituresList()}
        {renderTotalExpenditures()}
      </tbody>
    </Table>
  )

  return (
    <ErrorBoundary>
      {Object.keys(expenditures).length === 0 && renderNoExpenditures()}
      {Object.keys(expenditures).length > 0 && renderExpendituresTable()}
    </ErrorBoundary>
  )
}

export default ExpendituresTable