import { createSlice } from '@reduxjs/toolkit'
import C from '../../constants'
const sliceName = C.sliceNames.clubDetails

const initialState = {
  clubDetails: C.leagues[0].clubs[0],
  clubName: C.leagues[0].clubs[0].name,
  league: C.leagues[0].name
}

export const clubDetailsSlice = createSlice({
  name: sliceName,
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  // Redux Toolkit allows us to write "mutating" logic in reducers. It
  // doesn't actually mutate the state because it uses the Immer library,
  // which detects changes to a "draft state" and produces a brand new
  // immutable state based off those changes
  reducers: {
    resetClubDetailsState: () => {
      return initialState
    },
    setClubDetails: (state, action) => {
      state.clubDetails = action.payload
    },
    setClubName: (state, action) => {
      state.clubName = action.payload
    }
  }
})

export const {
  resetClubDetailsState,
  setClubDetails,
  setConfirmed,
  setClubName
} = clubDetailsSlice.actions

// selectors
export const selectClubDetails = state => state[sliceName].clubDetails
export const selectClubName = state => state[sliceName].clubName

export default clubDetailsSlice.reducer
