import { useState } from 'react'
import { Button, Col, Modal, Row } from 'react-bootstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEnvelopeOpenText } from '@fortawesome/free-solid-svg-icons'

const ActiveEventsAlert = props => {
  const [showEvents, setShowEvents] = useState(false)

  const renderEvents = () => props.activeEvents.map((event, i) => (
    <Row key={`event-row-${i}`}>
      <p>{event.type}</p>
    </Row>
  ))

  const renderModal = () => (
    <Modal
      className='club-details-modal-dialog'
      show={showEvents}
      onHide={() => setShowEvents(!showEvents)}
    >
      <Modal.Header className='club-details-modal'>
        TODO: Move to /office!
        These items require your attention:
      </Modal.Header>
      <Modal.Body className='club-details-modal'>
        {renderEvents()}
      </Modal.Body>
      <Modal.Footer className='club-details-modal'>
        <Button
          className='common-btn'
          onClick={props.clearEvents}
        >
          Clear events
        </Button>
      </Modal.Footer>
    </Modal>
  )

  return (
    <Col>
      <FontAwesomeIcon icon={faEnvelopeOpenText} /> You have {props.activeEvents.length} items requiring attention. <FontAwesomeIcon icon={faEnvelopeOpenText} />
      <Button
        className='common-btn'
        onClick={() => setShowEvents(true)}>
          View items
        </Button>
      {showEvents && renderModal()}
    </Col>
  )
}

export default ActiveEventsAlert