import { createSlice } from '@reduxjs/toolkit'
import C from '../../constants'
const sliceName = C.sliceNames.staff

const initialState = {
  academyDirector: false,
  ceo: false,
  coach: false,
  dof: false,
  manager: false
}

export const staffSlice = createSlice({
  name: sliceName,
  initialState,
  reducers: {
    resetStaffState: () => {
      return initialState
    },
    setStaffMember: (state, action) => {
      state[action.payload.role] = action.payload.person
    }
  }
})

export const {
  resetStaffState,
  setStaffMember
} = staffSlice.actions

// selectors
export const selectAcademyDirector = state => state[sliceName].academyDirector
export const selectCeo = state => state[sliceName].ceo
export const selectDof = state => state[sliceName].dof
export const selectManager = state => state[sliceName].manager
export const selectCoach = state => state[sliceName].coach
export const selectStaff = state => ([
  state[sliceName].academyDirector,
  state[sliceName].ceo,
  state[sliceName].coach,
  state[sliceName].dof,
  state[sliceName].manager
])

export default staffSlice.reducer
