import { createSlice } from '@reduxjs/toolkit'
import C from '../../constants'
import getRandomValue from '../../commonHelpers/getRandomValue'
import initEstimateStadiumValue from './helpers/estimators/stadium'
import initEstimateTrainingGroundValue from './helpers/estimators/trainingGround'
import initEstimateAssetValue from './helpers/estimateAssetValue'

const sliceName = C.sliceNames.finances
const assetEstimatorFunctions = {
  [C.assets.trainingGround.key]: initEstimateTrainingGroundValue,
  [C.assets.stadium.key]: initEstimateStadiumValue
}
const estimateAssetValue = initEstimateAssetValue({
  assetEstimatorFunctions,
  assetTypes: C.assets,
  getRandomValue
})

const initialState = {
  assets: [],
  clubFunds: 0,
  debts: [],
  availableSponsors: [],
  availableFunds: 0,
  expenditures: {
    stadium: [],
    training: [],
    trainingGround: [],
    wages: []
  },
  personalWealth: 0,
  revenues: {
    governmentGrant: [],
    seasonTickets: [],
    sponsors: [],
    tv: []
  },
  seasonShirtSales: 0,
  tickets: {
    availability: {
      corporate: 0,
      regular: 0,
      season: 0
    },
    prices: {
      corporate: 0,
      regular: 0,
      season: 0
    },
    sales: {
      corporateThisSeason: 0,
      regularThisSeason: 0,
      seasonTicketsThisSeason: 0,
      totalAllTime: 0,
      totalThisSeason: 0
    }
  }
}

export const financesSlice = createSlice({
  name: sliceName,
  initialState,
  reducers: {
    addAsset: (state, action) => {
      state.assets.push(
        estimateAssetValue({
          assetType: action.payload.assetType,
          payload: action.payload.payload
        })
      )
    },
    addClubFunds: (state, action) => {
      state.clubFunds += action.payload
    },
    addExpenditure: (state, action) => {
      const newExpenditure = {
        expenditureKey: action.payload.expenditureKey,
        expenditureName: action.payload.expenditureName,
        expenditureValue: action.payload.expenditureValue,
        expenditureWeeksRemaining: action.payload.expenditureWeeksRemaining
      }
      state.expenditures[action.payload.expenditureType].push(newExpenditure)
    },
    addPersonalWealth: (state, action) => {
      state.personalWealth += action.payload
    },
    addRevenue: (state, action) => {
      const newRevenue = {
        revenueKey: action.payload.revenueKey,
        revenueName: action.payload.revenueName,
        revenueValue: action.payload.revenueValue,
        revenueWeeksRemaining: action.payload.revenueWeeksRemaining
      }
      state.revenues[action.payload.revenueType].push(newRevenue)
    },
    degradeAsset: (state, action) => {
      state.assets.find(a => a.assetKey === action.payload).assetCondition --
    },
    removeExpenditure: (state, action) => {
      state.expenditures = state.expenditures.filter(r => r.type !== action.payload.type)
    },
    removeRevenue: (state, action) => {
      state.revenues = state.revenues.filter(r => r.type !== action.payload.type)
    },
    resetFinancesState: () => {
      return initialState
    },
    setAvailableSponsors: (state, action) => {
      state.availableSponsors = action.payload
    }
  }
})

export const {
  addAsset,
  addClubFunds,
  addExpenditure,
  addPersonalWealth,
  addRevenue,
  degradeAsset,
  removeExpenditure,
  removeRevenue,
  resetFinancesState,
  setAvailableSponsors,
} = financesSlice.actions

// selectors
export const selectAssets = state => state[sliceName].assets
export const selectAvailableSponsors = state => state[sliceName].availableSponsors
export const selectClubFunds = state => state[sliceName].clubFunds
export const selectExpenditures = state => state[sliceName].expenditures
export const selectPersonalWealth = state => state[sliceName].personalWealth
export const selectRevenues = state => state[sliceName].revenues
export const selectTicketDetails = state => state[sliceName].tickets

export default financesSlice.reducer
