const generateFinances = ({
  addAsset,
  addExpenditure,
  addRevenue,
  C,
  dispatch,
  getRandomValue
}) => ({
  clubDetails,
  league
}) => {
  const domesticTv = C.revenues.tv.types.find(r => r.key === 'domestic')
  const leagueMultiplier = league === 'Division 1' ? 4 : 1
  dispatch(
    addRevenue({
      revenueType: C.revenues.tv.key,
      revenueKey: domesticTv.key,
      revenueName: domesticTv.text,
      revenueValue: getRandomValue(
        (clubDetails.reputation * domesticTv.minMultiplier * leagueMultiplier),
        (clubDetails.reputation * domesticTv.maxMultiplier * leagueMultiplier)
      ),
      revenueWeeksRemaining: domesticTv.maxWeeks
    })
  )

  // Top division and high reputation lower division clubs own their own stadium.
  // Clubs that own these assets have these associated maintenance costs.
  // They also get overseas TV rights.
  const stadiumMaintenance = C.expenditures.stadium.types.find(e => e.key === 'maintenance')
  const trainingGroundMaintenance = C.expenditures.trainingGround.types.find(e => e.key === 'maintenance')
  if (league === 'Division 1' || clubDetails.reputation > 60) {
    const overseasTv = C.revenues.tv.types.find(r => r.key === 'overseas')
    dispatch(
      addRevenue({
        revenueType: C.revenues.tv.key,
        revenueKey: overseasTv.key,
        revenueName: overseasTv.text,
        revenueValue: getRandomValue(
          (clubDetails.reputation * overseasTv.minMultiplier * leagueMultiplier),
          (clubDetails.reputation * overseasTv.maxMultiplier * leagueMultiplier)
        ),
        revenueWeeksRemaining: overseasTv.maxWeeks
      })
    )
    dispatch(
      addAsset({
        assetType: C.assets.stadium.key,
        payload: clubDetails.stadium
      })
    )
    dispatch(
      addAsset({
        assetType: C.assets.trainingGround.key,
        payload: clubDetails.reputation
      })
    )
    dispatch(
      addExpenditure({
        expenditureType: C.expenditures.stadium.key,
        expenditureKey: stadiumMaintenance.key,
        expenditureName: stadiumMaintenance.text,
        expenditureValue: getRandomValue(
          (clubDetails.reputation * stadiumMaintenance.minMultiplier),
          (clubDetails.reputation * stadiumMaintenance.maxMultiplier)
        ),
        expenditureWeeksRemaining: getRandomValue(stadiumMaintenance.minWeeks, stadiumMaintenance.maxWeeks)
      })
    )
    dispatch(
      addExpenditure({
        expenditureType: C.expenditures.trainingGround.key,
        expenditureKey: trainingGroundMaintenance.key,
        expenditureName: trainingGroundMaintenance.text,
        expenditureValue: getRandomValue(
          (clubDetails.reputation * trainingGroundMaintenance.minMultiplier),
          (clubDetails.reputation * trainingGroundMaintenance.maxMultiplier)
        ),
        expenditureWeeksRemaining: getRandomValue(trainingGroundMaintenance.minWeeks, trainingGroundMaintenance.maxWeeks)
      })
    )
  }
  else {
    // clubs that don't own their stadium/training ground pay rent but not maintenance
    const stadiumRent = C.expenditures.stadium.types.find(e => e.key === 'rent')
    const trainingGroundRent = C.expenditures.trainingGround.types.find(e => e.key === 'rent')
    dispatch(
      addExpenditure({
        expenditureType: C.expenditures.stadium.key,
        expenditureKey: stadiumRent.key,
        expenditureName: stadiumRent.text,
        expenditureValue: getRandomValue(
          (clubDetails.reputation * stadiumRent.minMultiplier),
          (clubDetails.reputation * stadiumRent.maxMultiplier)
        ),
        expenditureWeeksRemaining: getRandomValue(stadiumRent.minWeeks, stadiumRent.maxWeeks)
      })
    )
    dispatch(
      addExpenditure({
        expenditureType: C.expenditures.trainingGround.key,
        expenditureKey: trainingGroundRent.key,
        expenditureName: trainingGroundRent.text,
        expenditureValue: getRandomValue(
          (clubDetails.reputation * trainingGroundRent.minMultiplier),
          (clubDetails.reputation * trainingGroundRent.maxMultiplier)
        ),
        expenditureWeeksRemaining: getRandomValue(trainingGroundRent.minWeeks, trainingGroundRent.maxWeeks)
      })
    )
  }
}

export default generateFinances