import { useSelector } from 'react-redux'
import Accordion from 'react-bootstrap/Accordion'
import Row from 'react-bootstrap/Row'
import ErrorBoundary from '../../commonComponents/ErrorBoundary'
import About from './components/About'
import AssetsTable from './components/AssetsTable'
import ExpendituresTable from './components/ExpendituresTable'
import RevenuesTable from './components/RevenuesTable'
import Funds from './components/Funds'
import {
  selectAssets,
  selectClubFunds,
  selectExpenditures,
  selectPersonalWealth,
  selectRevenues
} from './financesSlice'

const Finances = () => {
  const clubFunds = useSelector(selectClubFunds)
  const personalWealth = useSelector(selectPersonalWealth)
  const assets = useSelector(selectAssets)
  const expenditures = useSelector(selectExpenditures)
  const revenues = useSelector(selectRevenues)
  // TODO: pass as props
  const assetsTotal = assets
    .reduce((assetsTotal, asset) => assetsTotal + asset.assetValue, 0)
  const expenditureTotal = Object.keys(expenditures)
    .reduce((exps, exp) => exps.concat(expenditures[exp]), [])
    .flat()
    .reduce((expenditureTotal, expenditure) => expenditureTotal + expenditure.expenditureValue, 0)
  const revenueTotal = Object.keys(revenues)
    .reduce((revs, revenue) => revs.concat(revenues[revenue]), [])
    .flat()
    .reduce((revenueTotal, revenue) => revenueTotal + revenue.revenueValue, 0)
  const fundsProps = { clubFunds, personalWealth }

  const renderFunds = () => <Funds {...fundsProps} />

  const renderAssetsPanel = () => (
    <ErrorBoundary>
      <Accordion.Item eventKey='0'>
        <Accordion.Header>Fixed Assets: £{assetsTotal.toLocaleString()}</Accordion.Header>
        <Accordion.Body>
          <AssetsTable />
        </Accordion.Body>
      </Accordion.Item>
    </ErrorBoundary>
  )

  const renderRevenuesPanel = () => (
    <ErrorBoundary>
      <Accordion.Item eventKey='1'>
        <Accordion.Header>Weekly Revenues: £{revenueTotal.toLocaleString()}</Accordion.Header>
        <Accordion.Body>
          <RevenuesTable />
        </Accordion.Body>
      </Accordion.Item>
    </ErrorBoundary>
  )

  const renderExpendituresPanel = () => (
    <ErrorBoundary>
      <Accordion.Item eventKey='2'>
        <Accordion.Header>Weekly Expenditures: £{expenditureTotal.toLocaleString()}</Accordion.Header>
        <Accordion.Body>
          <ExpendituresTable />
        </Accordion.Body>
      </Accordion.Item>
    </ErrorBoundary>
  )

  const renderAccordion = () => (
    <Accordion flush>
      {renderAssetsPanel()}
      {renderRevenuesPanel()}
      {renderExpendituresPanel()}
    </Accordion>
  )

  const renderWeeklyNet = () => (
    <ErrorBoundary>
      <h3>Weekly revenue: £{(revenueTotal - expenditureTotal).toLocaleString()}</h3>
    </ErrorBoundary>
  )

  const renderAbout = () => <ErrorBoundary><About /></ErrorBoundary>
  return (
    <ErrorBoundary>
      <Row className='tile-row'>
        <h3 className='tourney'>Finances</h3>
        {renderFunds()}
      </Row>
      <Row className='tile-row'>
        {renderAbout()}
      </Row>
      <Row className='tile-row'>
        <h3>Financial Overview</h3>
        {renderAccordion()}
      </Row>
      <Row className='tile-row'>
        {renderWeeklyNet()}
      </Row>
    </ErrorBoundary>
  )
}

export default Finances