import { createSlice } from '@reduxjs/toolkit'
import C from '../../constants'
import getRandomValue from '../../commonHelpers/getRandomValue'
import initGenerateRandomEvents from './helpers/generateRandomEvents'
import eventGeneratorFunctions from './helpers/eventGeneratorFunctions'
const getRandomEvents = initGenerateRandomEvents({
  C,
  eventGeneratorFunctions,
  getRandomValue
})

const sliceName = C.sliceNames.office

const initialState = {
  events: []
}

export const officeSlice = createSlice({
  name: sliceName,
  initialState,
  reducers: {
    addNewEvent: (state, action) => {
      state.events.push(action.payload.event)
    },
    completeEvent: (state, action) => {
      state.events.splice(state.events.findIndex(e => e.id === action.payload), 1)
    },
    generateRandomEvents: (state, action) => {
      const events = getRandomEvents({
        assets: action.payload.assets,
        clubDetails: action.payload.clubDetails,
        staff: action.payload.staff,
        startIndex: state.events.length
      })
      if (events.length > 0) {
        if (state.events.length > 0) events.forEach(e => state.events.push(e))
        else state.events = events
      }
    },
    resetOfficeState: () => {
      return initialState
    }
  }
})

export const {
  addNewEvent,
  completeEvent,
  generateRandomEvents,
  resetOfficeState
} = officeSlice.actions

// selectors
export const selectEvents = state => state[sliceName].events

export default officeSlice.reducer
