import { useState } from 'react'
import {
  Button,
  Container,
  Col,
  Form
} from 'react-bootstrap'
import { useSelector, useDispatch } from 'react-redux'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
  faPlay,
  faToggleOff,
  faToggleOn
} from '@fortawesome/free-solid-svg-icons'
import {
  selectBackground,
  setBackground,
  selectName,
  setName,
  selectWealth,
  setWealth
} from '../owner/ownerSlice'
import {
  setGameState
} from '../overview/overviewSlice'
import { getName } from '../../commonHelpers/nameGenerator'
import C from '../../constants'

const PlayerDetails = () => {
  const background = useSelector(selectBackground)
  const name = useSelector(selectName)
  const wealth = useSelector(selectWealth)
  const dispatch = useDispatch()
  const [showWelcome, setShowWelcome] = useState(true)
  const [wealthOptions, setWealthOptions] = useState(C.owner.backgrounds.find(b => b.value === background).wealth)
  const defaultName = getName()

  const updateWealthOptions = value => {
    dispatch(setBackground(value))
    const wealthOptions = C.owner.backgrounds.find(background => background.value === value).wealth
    dispatch(setWealth(wealthOptions[0]))
    setWealthOptions(wealthOptions)
  }

  const renderBackgroundRow = () => (
    <div>
      <Form.Label>What's your background?</Form.Label>
      <Form.Control
        as='select'
        value={background}
        onChange={(e) => updateWealthOptions(e.target.value)}
      >
        {C.owner.backgrounds.map((background, i) => (
          <option
            key={`background-${i}`} value={background.value}>{background.value}</option>
        ))}
      </Form.Control>
    </div>
  )

  // autoFocus={true} is a hack for React Router re-rendering component and losing input focus
  const renderNameRow = () => (
    <div>
      <Form.Label>What's your name?</Form.Label>
      <Form.Control
        autoFocus={true}
        placeholder={`e.g. ${defaultName}`}
        value={name}
        onChange={(e) => dispatch(setName(e.target.value))}/>
    </div>
  )

  const renderStartOption = () => (
    <div>
      <div>
        <p>You are {name}, {background.toLowerCase()} extraordinaire!</p>
        <p>You have £{wealth.toLocaleString()} burning a hole in your pocket.</p>
        <p>Time to buy a football club and lead them to glory!</p>
      </div>
      <div>
        <Button
          className='common-btn confirmation-btn'
          onClick={() => dispatch(setGameState(C.gameStates.clubDetails))}
        >
        <div>Play! <FontAwesomeIcon icon={faPlay} /></div>
        </Button>
      </div>
    </div>
  )

  const renderToggleInfoRow = () => (
    <div>
      <Button
        className='common-btn toggle-btn'
        onClick={() => setShowWelcome(!showWelcome)}
      >
        <div>Toggle Info <FontAwesomeIcon icon={showWelcome ? faToggleOn : faToggleOff} /></div>
      </Button>
    </div>
  )

  const renderWealthRow = () => (
    <div>
      <Form.Label>How much are you worth??</Form.Label>
      <Form.Control
        as='select'
        value={wealth}
        onChange={(e) => dispatch(setWealth(e.target.value))}
      >
        {wealthOptions.map((option, i) => (
          <option
            key={`option-${i}`} value={option}>{`£${option.toLocaleString()}`}</option>
        ))}
      </Form.Control>
    </div>
  )

  const renderWelcomeRow = () => (
    <Container>
      <h1>Welcome to the game!</h1>
      <p>
        Set your details below to get started.
      </p>
    </Container>
  )

  const renderForm = () => (
    <Form>
      <Form.Group controlId='formGroupName'>
        {renderNameRow()}
      </Form.Group>
      <Form.Group controlId='formGroupBackground'>
        {renderBackgroundRow()}
      </Form.Group>
      <Form.Group controlId='formGroupWealth'>
        {renderWealthRow()}
      </Form.Group>
    </Form>
  )

  return (
    <Col xs={12} sm={9} md={6}>
      {showWelcome && renderWelcomeRow()}
      {renderToggleInfoRow()}
      {renderForm()}
      {(name && background && wealth) && renderStartOption()}
    </Col>
  )
}

export default PlayerDetails