const Headlines = props => {
  const result = props.lastResult[0] ? props.lastResult[0] : false
  const renderSeasonAndWeek = () => <div>Season {props.season} - Week {props.week}</div>
  const renderLatestResult = () => (
    <div>
      Last Result: { result ? 
        `${result.home.team} ${result.home.goals} - ${result.away.goals} ${result.away.team}`
        : 'N/A'
      }
    </div>
  )
  const renderNextMatch = () => <div>Next Match: Merseyside Blue vs Tyneside</div>
  const renderFunds = () => <div>Club Funds: £111,200,498</div>

  return (
    <>
      {renderSeasonAndWeek()}
      {renderLatestResult()}
      {renderNextMatch()}
      {renderFunds()}
    </>
  )
}

export default Headlines