module.exports = ({
  C,
  getName,
  getRandomValue
}) => ({
  role,
  clubDetails,
  position,
  squadNumber
}) => {
  const isPlayer = role === C.roles.roles.player
  let max = 100
  let min = 1
  if (position && position === C.roles.positions.gk) {
    max = clubDetails.squadRating.gk + 15
    min = clubDetails.squadRating.gk - 15
  }
  else if (position && position === C.roles.positions.def) {
    max = clubDetails.squadRating.def + 15
    min = clubDetails.squadRating.def - 15
  }
  else if (position && position === C.roles.positions.mid) {
    max = clubDetails.squadRating.mid + 15
    min = clubDetails.squadRating.mid - 15
  }
  else if (position && position === C.roles.positions.fw) {
    max = clubDetails.squadRating.fw + 15
    min = clubDetails.squadRating.fw - 15
  }
  const playerPosition = position
    ? position
    : Object.keys(C.roles.positions)[getRandomValue(0, 3)]
  const age = isPlayer
    ? getRandomValue(16, 40)
    : getRandomValue(28, 77)
  const contract = getRandomValue(1, 5)
  const rating = isPlayer
    ? getRandomValue(min, max, true) // normalise 1-100
    : getRandomValue(1, 100)
  const baseWage = getRandomValue(rating - 10, rating + 10)
  const wage = isPlayer
    ? baseWage * getRandomValue(100, 1000)
    : baseWage * 100

  const baseStats = {
    age,
    contract,
    name: getName(),
    rating: rating,
    role,
    wage
  }
  const person = C.roles.roles.staff.find(staffRole => staffRole.title === role)
    ? Object.assign(
      {},
      baseStats,
      { popularity: getRandomValue(1, 100) },
      { relationship: getRandomValue(1, 100) },
      { reputation: getRandomValue(1, 100) }
    )
    : Object.assign(
      {},
      baseStats,
      { position: playerPosition },
      { squadNumber }
    )

    return person
}
