import { useDispatch } from 'react-redux'
import { setStaffMember } from '../../staff/staffSlice'
import Button from 'react-bootstrap/Button'
import ErrorBoundary from '../../../commonComponents/ErrorBoundary'

const StaffQuit = props => {
  const dispatch = useDispatch()
  return (
    <ErrorBoundary>
      <div>Our {props.staff.role} has quit their job with immediate effect.</div>
      <div>{props.quitReason}.</div>
      <div>We might want to advertise for their replacement.</div>
      <div>
        Here is the profile we had for them - take a good look as I'll be deleting this for GDPR reasons:
        <ul className='common-list'>
          <li className='common-list-item'>Name: {props.staff.name}</li>
          <li className='common-list-item'>Job: {props.staff.role}</li>
          <li className='common-list-item'>Rating: {props.staff.rating}</li>
          <li className='common-list-item'>Reputation: {props.staff.reputation}</li>
          <li className='common-list-item'>Relationship with you: {props.staff.relationship}</li>
          <li className='common-list-item'>Weekly wage: {props.staff.wage}</li>
        </ul>
      </div>
      <Button
        className='common-btn'
        onClick={() => {
          dispatch(setStaffMember({
            person: false,
            role: props.staff.role
          }))
          props.completeEvent()
        }}
      >
        Accept Resignation
      </Button>
      <Button
        className='common-btn'
        onClick={() => {
          dispatch(setStaffMember({
            person: false,
            role: props.staff.role
          }))
          props.completeEvent()
        }}
      >
        Accept Resignation + Advertise For Replacement
      </Button>
    </ErrorBoundary>
  )
}

export default StaffQuit