import { combineReducers } from 'redux'
import clubDetailsReducer from '../features/clubDetails/clubDetailsSlice'
import financesReducer from '../features/finances/financesSlice'
import fixturesReducer from '../features/fixtures/fixturesSlice'
import officeReducer from '../features/office/officeSlice'
import overviewReducer from '../features/overview/overviewSlice'
import ownerReducer from '../features/owner/ownerSlice'
import playerDetailsReducer from '../features/playerDetails/playerDetailsSlice'
import squadReducer from '../features/squad/squadSlice'
import staffReducer from '../features/staff/staffSlice'

const reducers = {
  clubDetails: clubDetailsReducer,
  finances: financesReducer,
  fixtures: fixturesReducer,
  office: officeReducer,
  overview: overviewReducer,
  owner: ownerReducer,
  playerDetails: playerDetailsReducer,
  squad: squadReducer,
  staff: staffReducer
}

const rootReducer = combineReducers(reducers)

export default rootReducer
