const markRoundComplete = ({
  generateResult
}) => ({
  clubDetails,
  fixtures,
  nonPlayerClubs,
  round
}) => {
  const roundToComplete = fixtures[round]
    .map(fixture => {
      const home = fixture.home.team === clubDetails.name
        ? clubDetails
        : nonPlayerClubs.find(club => club.name === fixture.home.team)
      const away = fixture.away.team === clubDetails.name
        ? clubDetails
        : nonPlayerClubs.find(club => club.name === fixture.away.team)
      const teams = {
        home,
        away
      }
      let result = generateResult(teams)
      return result
    })
    .concat({ completed: true })
  fixtures[round] = roundToComplete
  return fixtures
}

export default markRoundComplete