import {
  useDispatch,
  useSelector
} from 'react-redux'
import {
  Button,
  Col,
  ListGroup,
  Row
} from 'react-bootstrap'
import {
  selectAvailableSponsors,
  selectSponsors,
  setAvailableSponsors,
  setSponsors
} from '../../overviewSlice'
import { addRevenue } from '../../../finances/financesSlice'
import ErrorBoundary from '../../../../commonComponents/ErrorBoundary'
import C from '../../../../constants'

const ConfirmSponsors = () => {
  const dispatch = useDispatch()
  const availableSponsors = useSelector(selectAvailableSponsors)
  const sponsors = useSelector(selectSponsors)

  const addSponsor = sponsor => {
    const newAvailableSponsors = availableSponsors.filter(availableSponsor => availableSponsor !== sponsor)
    dispatch(setSponsors(sponsors.concat(sponsor)))
    dispatch(addRevenue({
      revenueType: C.revenues.sponsors.key,
      revenueKey: sponsor.revenueKey,
      revenueName: sponsor.revenueName,
      revenueValue: sponsor.revenueValue,
      revenueWeeksRemaining: sponsor.revenueWeeksRemaining
    }))
    dispatch(setAvailableSponsors(newAvailableSponsors))
  }

  const renderSponsors = () => availableSponsors.map((sponsor, i) => (
    <ListGroup.Item key={i} className='common-list-item'>
      <p>Name: {sponsor.revenueName}</p>
      <p>Sponsor Type: {C.revenues.sponsors.types.find(s => s.key === sponsor.revenueKey).text}</p>
      {!C.revenues.sponsors.types.find(s => s.key === sponsor.revenueKey).multipleAllowed &&<p>Note: only one of this type of sponsor is allowed.</p>}
      <p>Total Value: £{(sponsor.revenueValue * sponsor.revenueWeeksRemaining).toLocaleString()}</p>
      <p>Weekly Income: £{(sponsor.revenueValue).toLocaleString()}</p>
      <p>Contract Length: {sponsor.revenueWeeksRemaining} weeks</p>
      <Button
        className='common-btn'
        onClick={() => addSponsor(sponsor)}
      >
        Add Sponsor
      </Button>
    </ListGroup.Item>
  ))

  return (
    <ErrorBoundary>
      <Row className='setup-row'>
        <Col className='setup-col'>
          <h3>Interested Sponsors:</h3>
          <h4>TODO:</h4>
          <p>Sort sponsors by type, value and length</p>
          <p>One list per type so it's easier to see if you can only have one of a given type</p>
          <ListGroup className='common-list setup-sponsors-list'>
            {availableSponsors.length > 0 && renderSponsors()}
            {availableSponsors.length === 0 && <p>No sponsors are interested at this time.</p>}
          </ListGroup>
          <a className='common-link' href={C.routes.setup}>Back to setup</a>
        </Col>
      </Row>
    </ErrorBoundary>
  )
}

export default ConfirmSponsors