import C from '../constants'
import { getName } from './nameGenerator'
import {
  addAsset,
  addClubFunds,
  addExpenditure,
  addRevenue,
  addPersonalWealth
} from '../features/finances/financesSlice'
import {
  incrementSeason,
  incrementWeek,
  setAvailableSponsors,
  setClubDetails as setOverviewClubDetails
} from '../features/overview/overviewSlice'
import {
  generateFixtures,
  setLeague as setClubLeague
} from '../features/fixtures/fixturesSlice'
import { setStaffMember } from '../features/staff/staffSlice'
import getRandomValue from './getRandomValue'
import { setGameState } from '../features/overview/overviewSlice'
import { setSquadList } from '../features/squad/squadSlice'

import initBuildSquad from '../features/squad/helpers/buildSquad'
import initGenerateFinances from './setup/generateFinances'
import initGetPerson from './getPerson'
import initGenerateSponsor from './generateSponsor'
import initSquadAndStaff from './setup/squadAndStaff'

const getPerson = initGetPerson({
  C,
  getName,
  getRandomValue
})
const buildSquad = initBuildSquad({
  C,
  getRandomValue,
  getPerson
})
const generateSponsor = initGenerateSponsor({ C, getRandomValue })

// import initGenerateExpenditure from '../features/finances/helpers/generateExpenditure'
// const generateExpenditure = initGenerateExpenditure({ C, getRandomValue })

const startGame = ({
  clubDetails,
  dispatch,
  league,
  wealth
}) => {
  dispatch(setGameState(C.gameStates.loading))
  // initialise state setting functions
  const setSquadAndStaff = initSquadAndStaff({
    addExpenditure,
    buildSquad,
    C,
    dispatch,
    getPerson,
    getRandomValue,
    setStaffMember,
    setSquadList
  })
  const generateFinances = initGenerateFinances({
    addAsset,
    addExpenditure,
    addRevenue,
    C,
    dispatch,
    getRandomValue
  })

  // initiate game setup like selecting sponsors and staff
  let initialSponsors = []
  const numberOfSponsors = Math.floor(clubDetails.reputation / 10)
  for (let i = 0; i < numberOfSponsors; i ++) {
    initialSponsors.push(generateSponsor({ clubDetails }))
  }
  dispatch(setAvailableSponsors(initialSponsors))
  
  // set season and week to 1
  dispatch(incrementSeason())
  dispatch(incrementWeek())

  // create the fixture list and set the club league
  dispatch(generateFixtures(league))
  dispatch(setClubLeague(league))

  // set club staff and playing squad
  // dispatch(setInitialOptionalStaff(clubDetails.reputation))
  // dispatch(setInitialRequiredStaff())
  // dispatch(setSquadList(clubDetails))
  setSquadAndStaff({ clubDetails })
  
  // subtract cost of club from player's wealth
  dispatch(addPersonalWealth(wealth - clubDetails.value))
  dispatch(addClubFunds(clubDetails.clubFunds))

  // set the club details into state
  dispatch(setOverviewClubDetails(clubDetails))
  generateFinances({
    clubDetails,
    league
  })
  dispatch(setGameState(C.gameStates.setup))
}

export default startGame