import Button from 'react-bootstrap/Button'
import Modal from 'react-bootstrap/Modal'
import ErrorBoundary from '../../../commonComponents/ErrorBoundary'
import AssetDamaged from './AssetDamaged'
import AssetDegraded from './AssetDegraded'
import SponsorshipOffer from './SponsorshipOffer'
import StaffQuit from './StaffQuit'

const EventModal = props => {
  const bodyProps = {
    completeEvent: props.completeEvent,
    event: props.event
  }

  const completeEvent = () => {
    console.log('completing EventModal')
    props.completeEvent()
    if (props.extraActions[props.event.key]) props.extraActions[props.event.key]()
  }

  const renderAcknowledge = () => (
    <Button
      className='common-btn'
      onClick={completeEvent}
    >
      Acknowledge Notification
    </Button>
  )

  const renderClose = () => (
    <Button className='common-btn' onClick={props.handleClose}>
      Close
    </Button>
  )

  const renderAssetDamaged = () => <AssetDamaged
    asset={props.event.eventDetail}
    completeEvent={props.completeEvent}
  />

  const renderAssetDegraded = () => <AssetDegraded {...bodyProps} />

  const renderSponsorshipOffer = () => <SponsorshipOffer
    sponsor={props.event.eventDetail}
    eventId={props.event.id}
    completeEvent={props.completeEvent}
  />

  const renderStaffQuit = () => <StaffQuit
    completeEvent={props.completeEvent}
    quitReason={props.event.eventDetail.quitReason}
    staff={props.event.eventDetail.staff}
  />

  const renderHeader = () => (
    <Modal.Header closeButton>
      <Modal.Title>{props.event.key}</Modal.Title>
    </Modal.Header>
  )

  const renderBody = () => (
    <ErrorBoundary>
      <Modal.Body>
        {props.event.key.match(/Damaged/) && renderAssetDamaged()}
        {props.event.key.match(/Degraded/) && renderAssetDegraded()}
        {props.event.key === 'sponsorshipOffer' && renderSponsorshipOffer()}
        {props.event.key === 'staffQuit' && renderStaffQuit()}
      </Modal.Body>
    </ErrorBoundary>
  )

  const renderFooter = () => {
    const nonBlocking = props.event.type === 'nonBlocking'
    return (
      <Modal.Footer>
        {nonBlocking && renderAcknowledge()}
        {!nonBlocking && renderClose()}
      </Modal.Footer>
    )
  }

  return (
    <ErrorBoundary>
      <Modal show={props.showModal} onHide={props.handleClose}>
        <ErrorBoundary>{renderHeader()}</ErrorBoundary>
        <ErrorBoundary>{renderBody()}</ErrorBoundary>
        <ErrorBoundary>{renderFooter()}</ErrorBoundary>
      </Modal>
    </ErrorBoundary>
  )
}

export default EventModal