import storage from 'redux-persist/lib/storage'
import { useDispatch } from 'react-redux'
import { Button } from 'react-bootstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPowerOff } from '@fortawesome/free-solid-svg-icons'
import { resetClubDetailsState } from '../features/clubDetails/clubDetailsSlice'
import { resetFinancesState } from '../features/finances/financesSlice'
import { resetOfficeState } from '../features/office/officeSlice'
import { resetOverviewState } from '../features/overview/overviewSlice'
import { resetPlayerDetailsState } from '../features/playerDetails/playerDetailsSlice'
import { resetStaffState } from '../features/staff/staffSlice'
import C from '../constants'

const NewGame = () => {
  const dispatch = useDispatch()

  const startNewGame = () => {
    storage.removeItem('persist:root')
    dispatch(resetClubDetailsState())
    dispatch(resetOverviewState())
    dispatch(resetPlayerDetailsState())
    dispatch(resetStaffState())
    dispatch(resetFinancesState())
    dispatch(resetOfficeState())
    window.location.reload()
  }

  return (
    <Button
      href={C.routes.home}
      className='common-btn new-game-btn'
      onClick={() => startNewGame()}
    >
      Start New Game <FontAwesomeIcon icon={faPowerOff} />
    </Button>
  )
}

export default NewGame