import { Table } from 'react-bootstrap'

const LeagueTable = props => {
  const renderTeamRows = () => props.leagueTable.map((team, i) => (
    <tr className='squat-row' key={`team-fixture-${i}`}>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {team.team}
      </td>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {team.p}
      </td>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {team.w}
      </td>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {team.d}
      </td>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {team.l}
      </td>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {team.gd}
      </td>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {team.pts}
      </td>
    </tr>
  ))

  const renderLeagueTableCol = () => (
    <Table className='fixture-list-table'>
      <thead className='fixture-list'>
        <tr>
          <th>
            <a
              className='common-link'
              href='/league-table'
            >
              Full Table
            </a>
          </th>
        </tr>
        <tr>
          <th>
            <p>{props.league} Standings</p>
          </th>
          <th>
            <p>W</p>
          </th>
          <th>
            <p>D</p>
          </th>
          <th>
            <p>L</p>
          </th>
          <th>
            <p>GD</p>
          </th>
          <th>
            <p>Pts</p>
          </th>
        </tr>
        {renderTeamRows()}
      </thead>
    </Table>
  )

  return renderLeagueTableCol()
}

export default LeagueTable
