module.exports = ({ C, getRandomValue, getPerson }) => ({ clubDetails }) => {
  let position
  const positions = C.roles.positions
  const squad = Array.from(new Array(clubDetails.squadSize))
    .map((_, i) => {
      const randomDecider = getRandomValue(1, 100)
      if (i === 0 || i === 11) position = positions.gk
      else if (i >= 1 && i <= 4) position = positions.def
      else if (i >= 5 && i <=8) position = positions.mid
      else if (i >= 9 && i <= 10) position = positions.fw
      else if (randomDecider <= 30) position = positions.def
      else if (randomDecider <= 65) position = positions.mid
      else if (randomDecider <= 95) position = positions.fw
      else position = positions.gk
      return getPerson({
        clubDetails,
        position,
        role: C.roles.roles.player,
        squadNumber: i + 1
      })
    })
  return squad
}
