import { createSlice } from '@reduxjs/toolkit'
// import initGenerateEvents from './helpers/generateEvents'
import removeClubFromArray from './helpers/removeClubFromArray'
import C from '../../constants'
const sliceName = C.sliceNames.overview
// const generateActiveEvents = initGenerateEvents(C.events)

const initialState = {
  activeEvents: [],
  availableSponsors: [],
  clubDetails: {},
  fixtures: [],
  gameState: C.gameStates.playerDetails,
  league: false,
  leagueTable: [],
  nonPlayerClubs: [
    C.clubsDivOne,
    C.clubsDivTwo,
    C.clubsDivThree,
    C.clubsDivFour
  ],
  season: 0,
  sponsors: [],
  week: 0
}

export const overviewSlice = createSlice({
  name: sliceName,
  initialState,
  reducers: {
    clearEvents: state => {
      state.activeEvents = []
    },
    generateEvents: state => {
      state.activeEvents = [] // generateActiveEvents()
    },
    incrementSeason: state => {
      state.season = state.season + 1
    },
    incrementWeek: state => {
      state.week = state.week + 1
    },
    resetOverviewState: () => {
      return initialState
    },
    setAvailableSponsors: (state, action) => {
      state.availableSponsors = action.payload
    },
    setClubDetails: (state, action) => {
      state.clubDetails = action.payload
      state.nonPlayerClubs = removeClubFromArray({ nonPlayerClubs: state.nonPlayerClubs, selectedClub: action.payload })
    },
    setGameState: (state, action) => {
      state.gameState = action.payload
    },
    setSponsors: (state, action) => {
      state.sponsors = action.payload
    },
    setWeekToOne: state => {
      state.week = 1
    }
  }
})

export const {
  clearEvents,
  generateEvents,
  incrementSeason,
  incrementWeek,
  resetOverviewState,
  setAvailableSponsors,
  setClubDetails,
  setGameState,
  setSponsors,
  setWeekToOne
} = overviewSlice.actions

// selectors
export const selectActiveEvents = state => state[sliceName].activeEvents
export const selectAvailableSponsors = state => state[sliceName].availableSponsors
export const selectClubDetails = state => state[sliceName].clubDetails
export const selectGameState = state => state[sliceName].gameState
export const selectNonPlayerClubs = state => state[sliceName].nonPlayerClubs
export const selectSeason = state => state[sliceName].season
export const selectSponsors = state => state[sliceName].sponsors
export const selectWeek = state => state[sliceName].week

export default overviewSlice.reducer
