export const getFacilitiesRating = facilities => {
  const average = Math.ceil(
    (facilities.corporate
    + facilities.training
    + facilities.youth) / 3
  )
  if (average > 89) return 'World class'
  else if (average > 79) return 'Excellent'
  else if (average > 64) return 'Promising'
  else if (average > 44) return 'Middling'
  else return 'Poor'
}

export const getFanExpectations = expectation => {
  if (expectation > 90) return 'Through the roof'
  else if (expectation > 75) return 'Pretty high'
  else if (expectation > 62) return 'Fair'
  else if (expectation > 45) return 'Low'
  else if (expectation > 32) return 'Very low'
  else return 'Basically none'
}

export const getFanHappiness = happiness => {
  if (happiness > 90) return 'I\'ll have what they\'re having!'
  else if (happiness > 80) return 'Loving life'
  else if (happiness > 70) return 'Really happy :)'
  else if (happiness > 60) return 'Cautiously optimistic'
  else if (happiness > 45) return 'Mumble grumble groan'
  else if (happiness > 32) return 'What is there to be happy about?'
  else return 'Sad all the time :('
}

export const getSquadHarmony = harmony => {
  if (harmony > 90) return 'Brothers in arms'
  else if (harmony > 80) return 'Will run through brick walls for each other'
  else if (harmony > 62) return 'Generally positive vibes'
  else if (harmony > 42) return 'There may be some issues here'
  else if (harmony > 32) return 'High school dramas everywhere'
  else return 'Not on speaking terms'
}

// TODO: adjust for league
export const getSquadRating = squadRating => {
  const average = Math.ceil(
    (squadRating.attack
    + squadRating.midfield
    + squadRating.defence
    + squadRating.goalkeeper) / 4
  )
  if (average > 89) return 'Scarily good'
  else if (average > 79) return 'Could go all the way'
  else if (average > 64) return 'There\'s a lot of promise here'
  else if (average > 44) return 'Bunch of plodders'
  else if (average > 32) return 'Perennial relegation dogfighters'
  else return 'Basically Sunday League'
}

export const getSquadSize = size => {
  if (size > 35) return 'We\'re gonna need a bigger boat'
  else if (size > 29) return 'Bloated'
  else if (size > 25) return 'Healthy'
  else if (size > 22) return 'Manageable'
  else if (size > 19) return 'On the small side'
  else return 'Anybody brought their boots?!'
}
