import getRandomValue from '../../../commonHelpers/getRandomValue'

const generateResult = teams => {
  const homeTeamWeighting = {
    gk: teams.home.squadRating.gk - teams.away.squadRating.gk,
    def: teams.home.squadRating.def - teams.away.squadRating.def,
    mid: teams.home.squadRating.mid - teams.away.squadRating.mid,
    fw: teams.home.squadRating.fw - teams.away.squadRating.fw
  }
  const homeWeightAverage = (
    homeTeamWeighting.gk
    + homeTeamWeighting.def
    + homeTeamWeighting.mid
    + homeTeamWeighting.fw
  ) / 4
  const homeMax = homeWeightAverage < 0
    ? 1
    : homeWeightAverage < 10
      ? 2
      : homeWeightAverage < 50
        ? 5
        : 3
  const awayMax = -homeWeightAverage < 0
    ? 1
    : -homeWeightAverage < 10
      ? 2
      : -homeWeightAverage < 50
        ? 5
        : 3

  return {
    home: {
      team: teams.home.name,
      goals: getRandomValue(0, homeMax)
    },
    away: {
      team: teams.away.name,
      goals: getRandomValue(0, awayMax)
    }
  }
}

export default generateResult