import { Table } from 'react-bootstrap'

const FixtureList = props => {
  // TODO: make fixtures/results one list and separate by .completed
  const teamFixtures = props.fixtures
    .filter((_, i) => i > props.week -1 && i < props.week + 5)
    .map(gameweek => gameweek.filter(fixture => fixture.home.team === props.team || fixture.away.team === props.team))

  const teamResults = props.week > 1
    ? props.fixtures
      .filter((_, i) => i <= props.week -2 && i >= props.week - 6)
      .map(gameweek => gameweek.filter(fixture => !fixture.completed && (fixture.home.team === props.team || fixture.away.team === props.team))
    )
    : []
  
  const renderFixtures = () => teamFixtures.map((fixture, i) => (
    <tr className='squat-row' key={`team-fixture-${i}`}>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {`${fixture[0].home.team} vs ${fixture[0].away.team}`}
      </td>
    </tr>
  ))

  const renderResults = () => teamResults.map((fixture, i) => (
    <tr className='squat-row' key={`team-result-${i}`}>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {`${fixture[0].home.team} ${fixture[0].home.goals}-${fixture[0].away.goals} ${fixture[0].away.team}`}
      </td>
    </tr>
  ))

  const renderFixturesTable = () => (
    <Table className='fixture-list-table'>
      <thead className='fixture-list'>
        <tr>
          <th>
            <a
              className='common-link'
              href='/fixtures'
            >
              Fixture Hub
            </a>
          </th>
        </tr>
        <tr className='squad-row'>
          <td>Upcoming Fixtures</td>
        </tr>
      </thead>
      <tbody>
        {renderFixtures()}
      </tbody>
    </Table>
  )

  const renderResultsTable = () => (
    <Table className='results-table'>
      <thead className='results-list'>
      <tr className='squad-row'>
        <td>Recent Results</td>
      </tr>
      </thead>
      <tbody>
        {renderResults()}
      </tbody>
    </Table>
  )

  return (
    <>
      {renderFixturesTable()}
      {teamResults.length > 0 && renderResultsTable()}
    </>
  )
}

export default FixtureList
