/**
 * 
 * @param {number} n
 * @param {results} array
 * @param {team} object 
 * @returns array of results
 */
const getLastNResults = ({
  n,
  results,
  team
}) => {
  if (!n && !team) return results
  const numberOfResults = n ? n : results.length
  const allCompletedFixtures = results
    .filter(gameweek => (
      gameweek.find(gw => gw.completed)
    ))
  const nCompletedFixtures = allCompletedFixtures
    .slice(allCompletedFixtures.length - numberOfResults, allCompletedFixtures.length)
  return team
    ? nCompletedFixtures
      .map(gameweek => gameweek
        .find(fixture => {
          if (fixture.completed) return
          return (fixture.home.team === team || fixture.away.team === team)
        }
      ))
    : nCompletedFixtures
      .map(gameweek => gameweek
        .filter(fixture => {
          return !fixture.completed
        })
      )
}

export default getLastNResults