const estimateTrainingGroundValue = ({ getRandomValue }) => reputation => {
  // take stadium condition and reduce value for every point under 100
  const trueValue = (reputation) * 100000
  const maxValue = trueValue * 1.15
  const minValue = trueValue * 0.85
  return getRandomValue(minValue, maxValue)
}

export default estimateTrainingGroundValue