// initialise with object of estimator functions with keys from C.assets, C.assets and getRandomValue helper
const estimateAssetValue = ({
  assetEstimatorFunctions,
  assetTypes, // C.assets
  getRandomValue
}) => ({ payload, assetType }) => {
  // initialise the correct estimator function for the asset type provided
  const estimatorFunction = assetEstimatorFunctions[assetTypes[assetType].key]({
    getRandomValue
  })
  const assetCondition = getRandomValue(50, 100) // TODO: get from club details
  const assetKey = assetTypes[assetType].key
  const assetValue = estimatorFunction(payload)
  return {
    assetCondition,
    assetKey,
    assetValue
  }
}

export default estimateAssetValue