import { useDispatch, useSelector } from 'react-redux'
import { addClubFunds, selectClubFunds } from '../../finances/financesSlice'
import Button from 'react-bootstrap/Button'
import ErrorBoundary from '../../../commonComponents/ErrorBoundary'

const AssetDamaged = props => {
  const dispatch = useDispatch()
  const clubFunds = useSelector(selectClubFunds)
  return (
    <ErrorBoundary>
      <div>Unfortunately our {props.asset.assetKey} has been damaged.</div>
      <div>It will cost £{props.asset.repairCost.toLocaleString()} to repair.</div>
      <div>This will leave £{(clubFunds - props.asset.repairCost).toLocaleString()} in the club's bank account.</div>
      <Button
        className='common-btn'
        onClick={() => {
          dispatch(addClubFunds(-props.asset.repairCost))
          props.completeEvent()
        }}
      >
        Authorise Repair
      </Button>
    </ErrorBoundary>
  )
}

export default AssetDamaged