import React from 'react'
import { Table } from 'react-bootstrap'
import { useSelector } from 'react-redux'
import {
  selectLeague,
  selectLeagueTable
} from './fixturesSlice'
import ErrorBoundary from '../../commonComponents/ErrorBoundary'

const LeagueTable = () => {
  const league = useSelector(selectLeague)
  const leagueTable = useSelector(selectLeagueTable)

  const renderTeamRows = () => leagueTable.map((team, i) => (
    <tr className='squat-row' key={`team-fixture-${i}`}>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {i + 1}
      </td>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {team.team}
      </td>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {team.p}
      </td>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {team.w}
      </td>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {team.d}
      </td>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {team.l}
      </td>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {team.gd}
      </td>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {team.pts}
      </td>
    </tr>
  ))

  const renderLeague = () => (
    <h3 className='tourney'>{league} Standings</h3>
  )

  const renderLeagueTable = () => (
    <Table className='fixture-list-table'>
      <thead className='fixture-list'>
        <tr>
          <th />
          <th>
            Team
          </th>
          <th>
            <p>P</p>
          </th>
          <th>
            <p>W</p>
          </th>
          <th>
            <p>D</p>
          </th>
          <th>
            <p>L</p>
          </th>
          <th>
            <p>GD</p>
          </th>
          <th>
            <p>Pts</p>
          </th>
        </tr>
        {renderTeamRows()}
      </thead>
    </Table>
  )

  return (
    <ErrorBoundary>
      {renderLeague()}
      {renderLeagueTable()}
    </ErrorBoundary>
  )
}

export default LeagueTable