import { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Row from 'react-bootstrap/Row'
import Table from 'react-bootstrap/Table'
import ErrorBoundary from '../../commonComponents/ErrorBoundary'
import EventModal from './components/EventModal'
import {
  degradeAsset
} from '../finances/financesSlice'
import {
  completeEvent,
  selectEvents
} from './officeSlice'

const Office = () => {
  const [selectedEvent, setSelectedEvent] = useState(false)
  const dispatch = useDispatch()
  const events = useSelector(selectEvents)
  const extraActions = {
    stadiumDegraded: () => dispatch(degradeAsset('stadium')),
    trainingGroundDegraded: () => dispatch(degradeAsset('trainingGround'))
  }

  const renderEventsRows = () => events.map((event, i) => (
    <tr
      key={`event-row-${i}`}
      className={(i % 2 === 0 ? 'squat-row list-item-primary' : 'squat-row list-item-secondary')}
      onClick={() => setSelectedEvent(event)}
    >
      <ErrorBoundary><td>#{event.id}: {event.key}</td></ErrorBoundary>
      <ErrorBoundary><td>{event.text}</td></ErrorBoundary>
    </tr>
  ))

  const renderEventsTable = () => (
    <ErrorBoundary>
      <Table>
        <thead>
          <tr>
            <th>Notification Type</th>
            <th>Information</th>
          </tr>
        </thead>
        <tbody>
          {renderEventsRows()}
        </tbody>
      </Table>
    </ErrorBoundary>
  )

  const renderEventModal = () => (
    <ErrorBoundary>
      <EventModal
        completeEvent={() => {
          dispatch(completeEvent(selectedEvent.id))
          setSelectedEvent(false)
        }}
        event={selectedEvent}
        extraActions={extraActions}
        handleClose={() => setSelectedEvent(false)}
        showModal={true}
      />
    </ErrorBoundary>
  )

  return (
    <>
      <ErrorBoundary>
        <Row className='tile-row'>
          <h3 className='tourney'>Office</h3>
          <p>You have {events.length} notifications.</p>
        </Row>
        <Row className='tile-row'>
          {selectedEvent && renderEventModal()}
          {renderEventsTable()}
        </Row>
      </ErrorBoundary>
    </>
  )
}

export default Office