import { useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {
  Button,
  Card,
  Col,
  Form,
  ListGroup,
  Modal,
  Row
} from 'react-bootstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
  faArrowCircleLeft,
  faCreditCard,
  faEject,
  faLock,
  faPoundSign,
  faToggleOff,
  faToggleOn
} from '@fortawesome/free-solid-svg-icons'
import C from '../../constants'
import {
  setClubDetails,
  setClubName,
  selectClubDetails,
  selectClubName
} from './clubDetailsSlice'
import { selectWealth } from '../owner/ownerSlice'
import { setGameState } from '../overview/overviewSlice'
import {
  getFacilitiesRating,
  getFanExpectations,
  getFanHappiness,
  getSquadHarmony,
  getSquadRating,
  getSquadSize
} from './helpers'
import startGame from '../../commonHelpers/startGame'

const ClubDetails = () => {
  const dispatch = useDispatch()
  const club = useSelector(selectClubName)
  const wealth = useSelector(selectWealth)
  const clubDetails = useSelector(selectClubDetails)
  const [selectedBrowseClub, setSelectedBrowseClub] = useState(club)
  const [league, setLeague] = useState(C.leagues[0].name)
  const [availableClubs, setAvailableClubs] = useState(C.leagues[0].clubs)
  const [showWelcome, setShowWelcome] = useState(true)
  const [showConfirm, setShowConfirm] = useState(false)

  const filterClubsByLeague = league => {
    const clubs = C.leagues.find(l => l.name === league).clubs
    setLeague(league)
    setAvailableClubs(clubs)
    setSelectedBrowseClub(clubs[0].name)
  }

  const renderClubDetails = () => {
    const details = C.leagues
      .find(l => l.name === league)
      .clubs
      .find(c => c.name === selectedBrowseClub)
    dispatch(setClubDetails(details))
    const squadRating = getSquadRating(details.squadRating)
    const facilities = getFacilitiesRating(details.facilities)
    const affordable = wealth >= details.value
    const buttonText = affordable
      ? (<div>I'LL TAKE IT! <FontAwesomeIcon icon={faCreditCard} /></div>)
      : (<div><FontAwesomeIcon icon={faLock} /> Hmm, too pricey...</div>)
    return (
      <Row>
        <Col>
          <Card className='club-details-list-card'>
            <ListGroup className='club-details-list'>
              <ListGroup.Item className='club-details-club-name list-item-primary'>
                Club name: {details.name}
              </ListGroup.Item>
              <ListGroup.Item className='list-item-secondary'>
                <span className='club-details-bold'>Club value:</span> £{details.value.toLocaleString()}
              </ListGroup.Item>
              <ListGroup.Item className='list-item-primary'>
                <span className='club-details-bold'>Squad rating: {squadRating}</span>
              </ListGroup.Item>
              <ListGroup.Item className='list-item-secondary'>
                <span className='club-details-bold'>Facilities rating: {facilities}</span>
              </ListGroup.Item>
              <ListGroup.Item className='list-item-primary'>
                <span className='club-details-bold'>Stadium: {details.stadium.name} ({details.stadium.capacity})</span>
              </ListGroup.Item>
              <ListGroup.Item className='list-item-secondary'>
                <span className='club-details-bold'>Squad size: {getSquadSize(details.squadSize)}</span>
              </ListGroup.Item>
              <ListGroup.Item className='list-item-primary'>
                <span className='club-details-bold'>Squad harmony: {getSquadHarmony(details.squadHarmony)}</span>
              </ListGroup.Item>
              <ListGroup.Item className='list-item-secondary'>
                <span className='club-details-bold'>Fan expectations: {getFanExpectations(details.fanExpectation)}</span>
              </ListGroup.Item>
              <ListGroup.Item className='list-item-primary'>
                <span className='club-details-bold'>Fan happiness: {getFanHappiness(details.fanHappiness)}</span>
              </ListGroup.Item>
            </ListGroup>
          </Card>
          <Button
            className={affordable ? 'common-btn confirmation-btn' : 'common-btn disabled-btn'}
            onClick={() => selectClubToBuy()}
          >
            {buttonText}
          </Button>
          <Button
            className='common-btn back-btn'
            onClick={() => dispatch(setGameState(C.gameStates.playerDetails))}
          >
            <div><FontAwesomeIcon icon={faArrowCircleLeft} /> Back to my details</div>
          </Button>
        </Col>
      </Row>
    )
  }

  const renderConfirmModal = () => {
    const remainder = (wealth - clubDetails.value).toLocaleString()
    return (
      <Modal
        show={showConfirm}
        onHide={() => setShowConfirm(false)}
        className='club-details-modal-dialog'
      >
        <Modal.Header className='club-details-modal' closeButton>
          Confirm your purchase
        </Modal.Header>
        <Modal.Body className='club-details-modal'>
          <p>You're about to buy {club} for £{clubDetails.value.toLocaleString()}.</p>
          <p>This will leave you with £{remainder} to spend on the running of your new plaything. Er, that is, club.</p>
        </Modal.Body>
        <Modal.Footer className='club-details-modal'>
          <Button
            href={C.routes.setup}
            className='common-btn club-details-confirm-buy-btn'
            onClick={() => startGame({
              clubDetails,
              dispatch,
              league,
              wealth
            })}
          >
            Shut up and take my money! <FontAwesomeIcon icon={faPoundSign} /><FontAwesomeIcon icon={faPoundSign} /><FontAwesomeIcon icon={faPoundSign} />
          </Button>
          <Button
            className='common-btn cancel-btn'
            onClick={() => setShowConfirm(false)}
          >
            <div>I'm having second thoughts... <FontAwesomeIcon icon={faEject} /></div>
          </Button>
        </Modal.Footer>
      </Modal>
    )
  }

  const renderFullClubListRow = () => (
    <div>
      <Form.Label>Let's see the brochure for...</Form.Label>
      <Form.Control
        as='select'
        value={selectedBrowseClub}
        onChange={(e) => setSelectedBrowseClub(e.target.value)}
      >
        {availableClubs.map((club, i) => (
          <option
            key={`club-${i}`} value={club.name}>{club.name}</option>
        ))}
      </Form.Control>
    </div>
  )

  const renderSelectLeagueRow = () => (
    <div>
      <Form.Label>Which division am I shopping in?</Form.Label>
      <Form.Control
        as='select'
        value={league}
        onChange={(e) => filterClubsByLeague(e.target.value)}
      >
        {C.leagues.map((league, i) => (
          <option
            key={`league-${i}`} value={league.name}>{league.name}</option>
        ))}
      </Form.Control>
    </div>
  )

  const renderToggleInfoRow = () => (
    <div>
      <Button
        className='common-btn toggle-btn'
        onClick={() => setShowWelcome(!showWelcome)}
      >
        <div>Toggle Info <FontAwesomeIcon icon={showWelcome ? faToggleOn : faToggleOff} /></div>
      </Button>
    </div>
  )

  const renderWelcomeRow = () => (
    <div>
      <div className='welcome'>
        <p>Which club should I buy? I love shopping!</p>
      </div>
    </div>
  )

  const renderForm = () => (
    <Form>
      <Form.Group>
        {renderSelectLeagueRow()}
      </Form.Group>
      <Form.Group>
        {renderFullClubListRow()}
      </Form.Group>
    </Form>
  )

  const selectClubToBuy = () => {
    dispatch(setClubName(selectedBrowseClub))
    setShowConfirm(true)
  }

  return (
    <Col xs={12} sm={9} md={6}>
      {showWelcome && renderWelcomeRow()}
      {renderToggleInfoRow()}
      {renderForm()}
      {renderClubDetails()}
      {showConfirm && renderConfirmModal()}
    </Col>
  )
}

export default ClubDetails