import { Table } from 'react-bootstrap'

const StaffList = props => {
  const renderStaffRows = () => props.staff.map((staff, i) => (
    <tr className='squat-row' key={`staff-row-${i}`}>
      <td className={i % 2 === 0 ? 'list-item-primary' : 'list-item-secondary'}>
        {staff.role}
      </td>
      <td className={i % 2 === 0 ? 'list-item-primary' : 'list-item-secondary'}>
        {staff.name}
      </td>
      <td className={i % 2 === 0 ? 'list-item-primary' : 'list-item-secondary'}>
        {staff.rating}
      </td>
    </tr>
  ))

  const renderStaffCol = () => (
    <>
      <a
        className='common-link'
        href='/staff'
      >
        Staff Hub
      </a>
      <Table className='staff-list-table'>
        <thead className='staff-list'>
          <tr>
            <td>Role</td>
            <td>Name</td>
            <td>Rating</td>
          </tr>
          {renderStaffRows()}
        </thead>
      </Table>
    </>
  )

  return renderStaffCol()
}

export default StaffList