import { Container, Row } from 'react-bootstrap'
import { useSelector, useDispatch } from 'react-redux'
import {
  clearEvents,
  incrementWeek,
  incrementSeason,
  selectActiveEvents,
  selectClubDetails,
  selectGameState,
  selectNonPlayerClubs,
  selectSeason,
  selectWeek,
  setWeekToOne
} from './overviewSlice'
import {
  generateRandomEvents
} from '../office/officeSlice'
import { selectStaff } from '../staff/staffSlice'
import {
  selectSquadList
} from '../squad/squadSlice'
import {
  selectAssets
} from '../finances/financesSlice'
import {
  completeFixtureRound,
  selectFixtures,
  selectLeague,
  selectLeagueTable
} from './../fixtures/fixturesSlice'
import isEndOfSeason from '../../commonHelpers/isEndOfSeason'
import getLastNResults from '../../commonHelpers/getLastNResults'

import localConstants from './constants'
import C from '../../constants'

import ActiveEvents from './components/ActiveEventsAlert'
import AdvanceGame from '../../commonComponents/AdvanceGame'
import FixtureList from './components/FixtureList'
import Headlines from './components/Headlines'
import LeagueTable from './components/LeagueTable'
import Loading from '../../commonComponents/Loading'
import Staff from './components/Staff'
import SquadList from './components/SquadList'
import Tile from '../../commonComponents/Tile'

const Overview = () => {
  const dispatch = useDispatch()
  const activeEvents = useSelector(selectActiveEvents)
  const assets = useSelector(selectAssets)
  const clubDetails = useSelector(selectClubDetails)
  const fixtures = useSelector(selectFixtures)
  const league = useSelector(selectLeague)
  const leagueTable = useSelector(selectLeagueTable)
  const staff = useSelector(selectStaff)
  const nonPlayerClubs = useSelector(selectNonPlayerClubs)
  const season = useSelector(selectSeason)
  const squadList = useSelector(selectSquadList)
  const week = useSelector(selectWeek)
  const lastResult = getLastNResults({ n: 1, results: fixtures, team: clubDetails.name })
  const gameReady = useSelector(selectGameState) === C.gameStates.inGame
  
  const activeEventsProps = {
    activeEvents,
    clearEvents: () => dispatch(clearEvents())
  }
  const advanceProps = {
    disabled: activeEvents.length > 0 ? true : false,
    onClick: isEndOfSeason(week)
    ? () => {
      dispatch(setWeekToOne())
      dispatch(incrementSeason())
    }
    : () => {
      dispatch(completeFixtureRound({
        clubDetails,
        league,
        nonPlayerClubs,
        round: week - 1
      }))
      dispatch(generateRandomEvents({
        assets,
        clubDetails,
        staff
      }))
      dispatch(incrementWeek())
    }
  }
  const fixturesProps = {
    fixtures,
    team: clubDetails.name,
    week
  }
  const leagueTableProps = {
    league,
    leagueTable
  }
  const headlinesProps = {
    lastResult,
    season,
    week
  }
  const staffProps = {
    dispatch,
    staff
  }
  const squadProps = {
    dispatch,
    squadList,
    squadRating: clubDetails.squadRating
  }

  const renderActiveEvents = () => <Tile><ActiveEvents {...activeEventsProps}/></Tile>
  const renderAdvanceGame = () => <Tile><AdvanceGame {...advanceProps} /></Tile>
  const renderFixtureListCol = () => <Tile><FixtureList {...fixturesProps} /></Tile>
  const renderLeagueTableCol = () => <Tile><LeagueTable {...leagueTableProps} /></Tile>
  const renderLoadingRow = () => <Tile><Loading text={localConstants.loadingText} /></Tile>
  const renderHeadlines = () => <Tile><Headlines {...headlinesProps} /></Tile>
  const renderStaffRow = () => <Tile><Staff {...staffProps} /></Tile>
  const renderSquadListRow = () => <Tile><SquadList {...squadProps} /></Tile>

  const renderGame = () => (
    <Row className='tile-row'>
      {renderHeadlines()}
      {activeEvents && activeEvents.length > 0 && renderActiveEvents()}
      {renderAdvanceGame()}
      {renderSquadListRow()}
      {renderFixtureListCol()}
      {renderLeagueTableCol()}
      {renderStaffRow()}
    </Row>
  )

  return (
    <Container fluid='xl'>
      {!gameReady && renderLoadingRow()}
      {gameReady && renderGame()}
    </Container>
  )
}

export default Overview