import { createSlice } from '@reduxjs/toolkit'
import C from '../../constants'
import buildFixtureList from './helpers/buildFixtureList'
import generatePreLeagueTable from './helpers/generatePreLeagueTable'
import generateResult from './helpers/generateResult'
import getTeamsForLeague from './helpers/getTeamsForLeague'
import initMarkRoundComplete from './helpers/markRoundComplete'
import updateLeagueTable from './helpers/updateLeagueTable'
const markRoundComplete = initMarkRoundComplete({ generateResult })
const sliceName = C.sliceNames.fixtures
const leagues = C.leagues

const initialState = {
  fixtures: [],
  league: false,
  leagueTable: []
}

export const fixturesSlice = createSlice({
  name: sliceName,
  initialState,
  reducers: {
    completeFixtureRound: (state, action) => {
      state.fixtures = markRoundComplete({
        clubDetails: action.payload.clubDetails,
        fixtures: state.fixtures,
        league: action.payload.league,
        nonPlayerClubs: action.payload.nonPlayerClubs[0],
        round: action.payload.round
      })
      state.leagueTable = updateLeagueTable(state.leagueTable, state.fixtures[action.payload.round])
    },
    generateFixtures: (state, action) => {
      const teams = getTeamsForLeague(leagues, action.payload)
      state.fixtures = buildFixtureList({ teams })
    },
    resetFixtureState: () => {
      return initialState
    },
    setLeague: (state, action) => {
      const teams = getTeamsForLeague(leagues, action.payload)
      state.league = action.payload
      state.leagueTable = generatePreLeagueTable(teams)
    },
    updateLeagueTable: (state, action) => {
      // action.payload == results array
      state.leagueTable = updateLeagueTable(state.leagueTable, action.payload)
    }
  }
})

export const {
  completeFixtureRound,
  generateFixtures,
  resetFixtureState,
  setLeague
} = fixturesSlice.actions

// selectors
export const selectFixtures = state => state[sliceName].fixtures
export const selectLeague = state => state[sliceName].league
export const selectLeagueTable = state => state[sliceName].leagueTable

export default fixturesSlice.reducer
