import { useSelector } from 'react-redux'
import Table from 'react-bootstrap/Table'
import ErrorBoundary from '../../../commonComponents/ErrorBoundary'
import C from '../../../constants'
import { selectRevenues } from '../financesSlice'

const RevenuesTable = () => {
  const revenues = useSelector(selectRevenues)
  const revenueTotal = Object.keys(revenues)
    .reduce((revs, rev) => revs.concat(revenues[rev]), [])
    .flat()
    .reduce((revenueTotal, revenue) => revenueTotal + revenue.revenueValue, 0)

    const revenuesList = Object.keys(revenues)
    .map(rev => ({
      type: C.revenues[rev].text,
      revenues: revenues[rev]
    }))
    .filter(rev => rev.revenues.length > 0)

  const renderTotalRevenues = () => (
    <tr>
      <td />
      <td>Total Revenues:</td>
      <td>{`£${revenueTotal.toLocaleString()}`}</td>
    </tr>
  )
  
  const renderRevenuesList = () => revenuesList
    .map(rev => rev.revenues.map((revenue, i) => (
      <tr className={
        (i % 2 === 0)
          ? 'squat-row list-item-primary'
          : 'squat-row list-item-secondary'
        }
        key={`revenue-row-${i}`}
      >
        <td>{rev.type}</td>
        <td>{revenue.revenueName ? revenue.revenueName : 'None'}</td>
        <td>{`£${revenue.revenueValue.toLocaleString()}`}</td>
      </tr>
    )
  ))

  const renderNoRevenues = () => <p>The club currently has no weekly revenues.</p>

  const renderRevenuesTable = () => (
    <Table>
      <thead>
        <tr>
          <td>Revenue Type</td>
          <td>Revenue Name</td>
          <td>Estimated Revenue Value</td>
        </tr>
      </thead>
      <tbody>
        {renderRevenuesList()}
        {renderTotalRevenues()}
      </tbody>
    </Table>
  )

  return (
    <ErrorBoundary>
      {Object.keys(revenues).length === 0 && renderNoRevenues()}
      {Object.keys(revenues).length > 0 && renderRevenuesTable()}
    </ErrorBoundary>
  )
}

export default RevenuesTable