// libs + utils
import React, { useEffect } from 'react'
import { useSelector } from 'react-redux'
import 'bootstrap/dist/css/bootstrap.min.css'
import WebFont from 'webfontloader'
import {
  BrowserRouter as Router,
  Switch,
  Route
} from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
  faCoins,
  faBriefcase,
  faHome,
  faTable,
  faUser,
  faUsers
} from '@fortawesome/free-solid-svg-icons'

import C from './constants'

// actions
import {
  selectGameState
} from './features/overview/overviewSlice'
import {
  selectEvents
} from './features/office/officeSlice'

// rendering components
import { Container, Nav, Row } from 'react-bootstrap'

import ClubDetails from './features/clubDetails/ClubDetails'
import ConfirmSponsors from './features/overview/setup/components/ConfirmSponsors'
import ErrorBoundary from './commonComponents/ErrorBoundary'
import Finances from './features/finances/Finances'
import FixturesHub from './features/fixtures/Fixtures'
import LeagueTable from './features/fixtures/LeagueTable'
import Loading from './commonComponents/Loading'
import NewGame from './commonComponents/NewGame'
import Office from './features/office/Office'
import Overview from './features/overview/Overview'
import OwnerHub from './features/owner/Owner'
import PlayerDetails from './features/playerDetails/PlayerDetails'
import ReleaseNotes from './features/releaseNotes/ReleaseNotes'
import Setup from './features/overview/setup/Setup'
import SquadHub from './features/squad/Squad'
import Staff from './features/staff/Staff'

// styles
import './App.scss'

const renderClubDetails = () => <Row className='main-row justify-content-md-center'><ClubDetails /></Row>
const renderLoading = () => <Row className='main-row justify-content-md-center'><Loading /></Row>
const renderOverview = () => <Row key='no-rerender' className='main-row justify-content-md-center'><Overview /></Row>
const renderPlayerDetails = () => <Row className='main-row justify-content-md-center'><PlayerDetails /></Row>

const App = () => {
  useEffect(() => {
    WebFont.load({
      google: {
        families: ['Nunito', 'Rubik', 'Tourney']
      }
    })
  }, [])
  
  const gameState = useSelector(selectGameState)
  const events = useSelector(selectEvents)

  const Main = () => (
    <main role='main'>
      <ErrorBoundary>
        {gameState === C.gameStates.playerDetails && renderPlayerDetails()}
      </ErrorBoundary>
      <ErrorBoundary>
        {(gameState === C.gameStates.clubDetails) && renderClubDetails()}
      </ErrorBoundary>
      <ErrorBoundary>
        {gameState === C.gameStates.loading && renderLoading()}
      </ErrorBoundary>
      <ErrorBoundary>
        {gameState === C.gameStates.inGame && renderOverview()}
      </ErrorBoundary>
    </main>
  )

  /**
  * TODO: add https://react-bootstrap.github.io/components/navs/#fill-and-justify
  * and Col Holvast as design manager
  */
  const renderHeader = () => (
    <ErrorBoundary>
      <Nav fill variant='tabs'>
        <Nav.Item>
          <Nav.Link className='common-nav-link ' href={C.routes.home}>
            Home <FontAwesomeIcon icon={faHome} />
          </Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link className='common-nav-link ' href={C.routes.me}>
            Me <FontAwesomeIcon icon={faUser} />
          </Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link className='common-nav-link ' href={C.routes.office}>
            Office <FontAwesomeIcon icon={faBriefcase} />{events.length > 0 && <p>({events.length})</p>}
          </Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link className='common-nav-link ' href={C.routes.finances}>
            Finances <FontAwesomeIcon icon={faCoins} />
          </Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link className='common-nav-link ' href={C.routes.leagueTable}>
            Table <FontAwesomeIcon icon={faTable} />
          </Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link className='common-nav-link ' href={C.routes.squad}>
            Squad <FontAwesomeIcon icon={faUsers} />
          </Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <NewGame />
        </Nav.Item>
      </Nav>
    </ErrorBoundary>
  )

  return (
    <ErrorBoundary>
      <Container className='app-container'>
        <Router>
          {renderHeader()}
          <hr />
          <Switch>
            <Route exact path={C.routes.finances}>
              <Finances />
            </Route>
            <Route exact path={C.routes.fixtures}>
              <FixturesHub />
            </Route>
            <Route exact path={C.routes.leagueTable}>
              <LeagueTable />
            </Route>
            <Route exact path={C.routes.me}>
              <OwnerHub />
            </Route>
            <Route exact path={C.routes.office}>
              <Office />
            </Route>
            <Route exact path={C.routes.releaseNotes}>
              <ReleaseNotes />
            </Route>
            <Route exact path={C.routes.setup}>
              <Setup />
            </Route>
            <Route exact path={C.routes.setupFinances}>
              <Finances />
            </Route>
            <Route exact path={C.routes.setupSponsors}>
              <ConfirmSponsors />
            </Route>
            <Route exact path={C.routes.squad}>
              <SquadHub />
            </Route>
            <Route exact path={C.routes.staff}>
              <Staff />
            </Route>
            <Route exact path={C.routes.home}>
              <Main />
            </Route>
          </Switch>
        </Router>
      </Container>
    </ErrorBoundary>
  )
}

export default App
