import {
  Col,
  Row
} from 'react-bootstrap'
import { useSelector } from 'react-redux'
import {
  selectBackground,
  selectName,
  selectWealth
} from './ownerSlice'
import { selectClubDetails } from '../overview/overviewSlice'
import ErrorBoundary from '../../commonComponents/ErrorBoundary'

const Owner = () => {
  const background = useSelector(selectBackground)
  const name = useSelector(selectName)
  const wealth = useSelector(selectWealth)
  const club = useSelector(selectClubDetails).name

  return (
    <ErrorBoundary>
      <Row>
        <Col>
          You are {name}.
          You made your money as a {background}.
          How much money? Well you have £{wealth.toLocaleString()} left.
          You own {club}.
        </Col>
      </Row>
    </ErrorBoundary>
  )
}

export default Owner