import { useState } from 'react'
import {
  Button,
  Col,
  Table
} from 'react-bootstrap'
import { useSelector } from 'react-redux'
import { selectFixtures } from './fixturesSlice'
import {
  selectClubDetails,
  selectWeek
} from '../overview/overviewSlice'
import ErrorBoundary from '../../commonComponents/ErrorBoundary'

const FixtureList = () => {
  const [showTeamFixtures, setShowTeamFixtures] = useState(true)
  const fixtures = useSelector(selectFixtures)
  const week = useSelector(selectWeek)
  const team = useSelector(selectClubDetails).name
  const gameweekFixtures = fixtures[week - 1]
  const teamFixtures = fixtures
    .filter((_, i) => (i > week -1 && i < week + 5)) // week = 1 get 0 - 5
    .map(gameweek => gameweek.filter(fixture => fixture.home.team === team || fixture.away.team === team))

  const renderGameweekFixtures = () => gameweekFixtures.map((fixture, i) => (
    <tr className='squat-row' key={`gameweek-fixture-${i}`}>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {`${fixture.home.team} vs ${fixture.away.team}`}
      </td>
    </tr>
  ))

  const renderTeamFixtures = () => teamFixtures.map((fixture, i) => (
    <tr className='squat-row' key={`team-fixture-${i}`}>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {`${fixture[0].home.team} vs ${fixture[0].away.team}`}
      </td>
    </tr>
  ))

  const renderFixturesCol = () => (
    <>
      <Col className='squat-row' xs={12} md={6} xl={4}>
        <Table className='fixture-list-table'>
          <thead className='fixture-list'>
            <tr>
              <th>
                <p>Fixtures</p>
                <Button
                  className='common-btn'
                  onClick={() => setShowTeamFixtures(!showTeamFixtures)}
                >
                  {showTeamFixtures ? 'View Gameweek Fixtures' : 'Show Team Fixtures'}
                </Button>
              </th>
            </tr>
            {showTeamFixtures && renderTeamFixtures()}
            {!showTeamFixtures && renderGameweekFixtures()}
          </thead>
        </Table>
      </Col>
    </>
  )

  return <ErrorBoundary>{renderFixturesCol()}</ErrorBoundary>
}

export default FixtureList
