import { createSlice } from '@reduxjs/toolkit'
import C from '../../constants'
const sliceName = C.sliceNames.owner

const initialState = {
  background: C.owner.backgrounds[0].value,
  name: '',
  popularity: 50,
  wealth: C.owner.backgrounds[0].wealth[0]
}

export const ownerSlice = createSlice({
  name: sliceName,
  initialState,
  reducers: {
    resetPlayerDetailsState: state => {
      state = initialState
    },
    setBackground: (state, action) => {
      state.background = action.payload
    },
    setName: (state, action) => {
      state.name = action.payload
    },
    setPopularity: (state, action) => {
      state.popularity = action.payload
    },
    setWealth: (state, action) => {
      state.wealth = action.payload
    }
  }
})

export const {
  setBackground,
  setName,
  setPopularity,
  setWealth
} = ownerSlice.actions

// selectors
export const selectBackground = state => state[sliceName].background
export const selectName = state => state[sliceName].name
export const selectPopularity = state => state[sliceName].popularity
export const selectWealth = state => state[sliceName].wealth

export default ownerSlice.reducer
