// import { useState } from 'react'
import { Table } from 'react-bootstrap'
import { useSelector } from 'react-redux'
import { selectSquadList } from './squadSlice'
import ErrorBoundary from '../../commonComponents/ErrorBoundary'
// import '../styles/SquadList.scss'
// import PlayerCard from './PlayerCard'

const SquadList = () => {
  const squadList = useSelector(selectSquadList)
  const salaryTotal = squadList.reduce((wageBill, player) => wageBill + player.wage, 0)
  // const [selectedPlayer, setSelectedPlayer] = useState(false)
  // const [showPlayerCard, setShowPlayerCard] = useState(false)

  // const playerCardProps = {
  //   selectedPlayer,
  //   showPlayerCard,
  //   setShowPlayerCard
  // }

  // const renderPlayerCard = playerCardProps => <PlayerCard {...staffCardProps} />

  const renderPlayerRows = () => squadList.map((player, i) => (
    <tr className={(i % 2 === 0) ? 'squat-row list-item-primary' : 'squat-row list-item-secondary'} key={`squad-member-${i}`}>
      <td>
        {player.squadNumber}
      </td>
      <td>
        {player.name}
      </td>
      <td>
        {player.position}
      </td>
      <td>
        {player.rating}
      </td>
      <td>
        {player.age}
      </td>
      <td>
        {player.contract}
      </td>
      <td>
        {`£${player.wage.toLocaleString()}`}
      </td>
    </tr>
  ))

  const renderSalaryRow = () => (
    <tr>
      <td />
      <td />
      <td />
      <td />
      <td />
      <td>Total: </td>
      <td className='list-item-primary'>
        {`£${salaryTotal.toLocaleString()}`}
      </td>
    </tr>
  )

  const renderSquadHeader = () => (
    <h3 className='tourney'>Squad Details</h3>
  )

  const renderSquadTable = () => (
    <Table className='squad-list-table'>
      <thead className='squad-list'>
        <tr>
          <th>Squad Number</th>
          <th>Name</th>
          <th>Position</th>
          <th>Rating</th>
          <th>Age</th>
          <th>Contract Length</th>
          <th>Weekly Wage</th>
        </tr>
      </thead>
      <tbody>
        {renderPlayerRows()}
        {renderSalaryRow()}
        {/* {renderOverviewRows()} */}
      </tbody>
    </Table>
  )

  return (
    <>
      <ErrorBoundary>
        {renderSquadHeader()}
        {renderSquadTable()}
      </ErrorBoundary>
      {/* {showPlayerCard && renderPlayerCard(playerCardProps)} */}
    </>
  )
}

export default SquadList
