import { useDispatch } from 'react-redux'
import {
  Button,
  Col,
  ListGroup,
  Row
} from 'react-bootstrap'
import { addRevenue } from '../../finances/financesSlice'
import ErrorBoundary from '../../../commonComponents/ErrorBoundary'
import C from '../../../constants'

const SponsorshipOffer = props => {
  const dispatch = useDispatch()

  const addSponsor = () => {
    props.completeEvent()
    dispatch(addRevenue({
      revenueType: C.revenues.sponsors.key,
      revenueKey: props.sponsor.revenueKey,
      revenueName: props.sponsor.revenueName,
      revenueValue: props.sponsor.revenueValue,
      revenueWeeksRemaining: props.sponsor.revenueWeeksRemaining
    }))
  }

  const renderSponsor = () => (
    <ListGroup.Item className='common-list-item'>
      <p>Name: {props.sponsor.revenueName}</p>
      <p>Sponsor Type: {C.revenues.sponsors.types.find(s => s.key === props.sponsor.revenueKey).text}</p>
      {!C.revenues.sponsors.types.find(s => s.key === props.sponsor.revenueKey).multipleAllowed &&<p>Note: only one of this type of sponsor is allowed.</p>}
      <p>Total Value: £{(props.sponsor.revenueValue * props.sponsor.revenueWeeksRemaining).toLocaleString()}</p>
      <p>Weekly Income: £{(props.sponsor.revenueValue).toLocaleString()}</p>
      <p>Contract Length: {props.sponsor.revenueWeeksRemaining} weeks</p>
      <Button
        className='common-btn'
        onClick={() => addSponsor()}
      >
        Add Sponsor
      </Button>
    </ListGroup.Item>
  )

  return (
    <ErrorBoundary>
      <Row className='setup-row'>
        <Col className='setup-col'>
          <ListGroup className='common-list sponsors-list'>
            {renderSponsor()}
          </ListGroup>
        </Col>
      </Row>
    </ErrorBoundary>
  )
}

export default SponsorshipOffer