// TODO: delete file here and reference /src/features/office/helpers/eventGeneratorFunctions
const surname = require('random-name')
const generateSponsor = ({
  C,
  getRandomValue
}) => ({
  clubDetails,
  sponsorType
}) => {
  // TODO: generate based on clubDetails e.g. wealth, reputation etc.
  // and have different sponsor types, i.e. either main (max 1 sponsor)
  // and secondary (max 20?) or e.g. kit sponsor, stadium sponsor, training ground sponsor,
  // noodle sponsor... yeah second option
  const sponsorTypes = C.revenues.sponsors.types
  const newSponsorType = sponsorType
    ? sponsorTypes.find(t => t.key === sponsorType)
    : sponsorTypes[Math.floor(Math.random() * sponsorTypes.length)]
  
  const max = clubDetails.reputation * newSponsorType.maxMultiplier
  const min = clubDetails.reputation * newSponsorType.minMultiplier

  const revenueKey = newSponsorType.key
  const revenueName = `${surname.last()} ${getRandomValue(0, 99)} Industries`
  const revenueValue = getRandomValue(min, max)
  // TODO: make bigger deals divisible by 52, i.e. per year
  const revenueWeeksRemaining = getRandomValue(
    newSponsorType.minWeeks,
    newSponsorType.maxWeeks
  )
  
  return {
    revenueKey,
    revenueName,
    revenueValue,
    revenueWeeksRemaining
  }
}

export default generateSponsor