import {
  Col,
  ListGroup,
  Table
} from 'react-bootstrap'
import ErrorBoundary from '../../commonComponents/ErrorBoundary'
import releaseNotes from './notes'

const renderChanges = changes => changes.map((change, i) => (
  <div key={`release-change-${i}`} className='release-change'>
    <ListGroup.Item className='release-note-type list-item-primary'>
      {change.type}
    </ListGroup.Item>
    <ListGroup.Item className='release-note-text list-item-secondary'>
      {change.text}
    </ListGroup.Item>
  </div>
))

const ReleaseNotes = () => {
  const renderReleases = () => releaseNotes.map((note, i) => (
    <tr key={`release-note-${i}`}>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {note.version}
      </td>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        {note.name}
      </td>
      <td className={(i % 2 === 0) ? 'list-item-primary' : 'list-item-secondary'}>
        <ListGroup variant='flush'>
          {renderChanges(note.changes)}
        </ListGroup>
      </td>
    </tr>
  ))

  const renderReleaseNotes = () => (
    <>
      <Col xs={12} xl={10}>
        <Table className='release-notes-table'>
          <thead className='release-notes'>
            <tr>
              <th>
                Version
              </th>
              <th>
                Release Name
              </th>
              <th>
                Changes
              </th>
            </tr>
          </thead>
          <tbody>
            {renderReleases()}
          </tbody>
        </Table>
      </Col>
    </>
  )

  return <ErrorBoundary>{renderReleaseNotes()}</ErrorBoundary>
}

export default ReleaseNotes
