// pass normalise as true to get ratings between 1 and 100
const getRandomValue = (min, max, normalise = false) => {
  const hardMax = (normalise && max > 100) ? 100 : max
  const hardMin = (normalise && max < 1) ? 1 : min
  return Math.floor(Math.random() * (hardMax - hardMin + 1) + hardMin)
}

export default getRandomValue
