const staffQuit = ({
  getRandomValue
}) => ({
  clubReputation,
  staffList
}) => {
  const staff = staffList.reduce((prev, current) =>
    prev.relationship < current.relationship ? prev : current)

  const reasonDice = getRandomValue(0, 100)

  let quitReason = 'They did not give a reason for leaving.'
  // TODO workout reason hierarchy
  if (staff.rating > clubReputation || staff.reputation > clubReputation) {
    if (reasonDice > 20) quitReason = 'They received an offer from a better club.'
    else quitReason = 'They received a huge offer from another club'
  }
  else if (staff.popularity < 40) quitReason = 'They felt unwanted by the fans.'
  else if (staff.relationship < 40) {
    if (reasonDice > 50) quitReason = 'They cited their inability to work with you as the reason for leaving.'
    else quitReason = 'They felt that, given the strained relationship with yourself, it was better to jump before they were pushed.'
  }

  return {
    quitReason,
    staff
  }
}

export default staffQuit